import React from 'react'
import {
  Datagrid,
  Filter,
  List,
  ListProps,
  Pagination,
  TextField,
  TextInput,
} from 'react-admin'
import { ResourceName } from '../../../../core/ResourceName'
import FilterProps from '../../../common/FilterProps'

const CameraServerFilters = (props: FilterProps) => (
  <Filter {...props}>
    <TextInput source="name" alwaysOn />
  </Filter>
)

const CameraServerGrid = ({ ...props }) => (
  <Datagrid
    {...props}
    size="small"
    rowClick={(id) => `${ResourceName.CAMERA_SERVERS}/${id}/show`}
    hasBulkActions={false}
  >
    <TextField source="id" label="ID" />
    <TextField source="name" />
    <TextField source="address" />
  </Datagrid>
)

export const CameraServerList = ({ ...props }: ListProps) => (
  <List
    {...props}
    perPage={20}
    pagination={<Pagination rowsPerPageOptions={[10, 20, 50]} />}
    exporter={false}
    sort={{
      field: 'id',
      order: 'DESC',
    }}
    filters={<CameraServerFilters />}
  >
    <CameraServerGrid />
  </List>
)
