import { createStyles, makeStyles, Theme } from '@material-ui/core'
import React, { useEffect, useReducer, useState } from 'react'
import {
  useMutation,
  useNotify,
  useRecordContext,
  useShowContext,
} from 'react-admin'
import { Authority } from '../../../core/auth/Authority'
import { EventDto } from '../../../core/dto/event/event.dto'
import { EventStatus } from '../../../core/enum/EventStatus'
import {
  activationButtonInitialState,
  toggleActivationButtonReducer,
} from '../../../core/events/handle-activation-button.reducer'
import Button from '../../common/customized-mui-components/Button'
import { useHasAuthority } from '../../hooks/useHasAuthority'
import { StatusTransitionDialog } from './StatusTransitionDialog'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    button: {
      color: theme.palette.common.white,
      marginRight: theme.spacing(1),
      marginLeft: theme.spacing(1),
    },
  }),
)

export const ToggleActivationButton = () => {
  const classes = useStyles()
  const notify = useNotify()
  const hasAuthority = useHasAuthority()
  const { loading, refetch } = useShowContext()
  const [{ type, text, mutateQuery }, dispatch] = useReducer(
    toggleActivationButtonReducer,
    activationButtonInitialState,
  )
  const event = useRecordContext<EventDto>()
  const [transitionDialogOpen, setTransitionDialogOpen] = useState(false)
  const [skipTransitionDialog, setSkipTransitionDialog] = useState(false)
  const [waitingForJobStart, setWaitingForJobStart] = useState(false)
  const [mutate, { loading: mutationLoading }] = useMutation()

  useEffect(() => {
    dispatch({ type: event.status })
  }, [event])

  useEffect(() => {
    if (!loading && !mutationLoading && !skipTransitionDialog) {
      if (event.status === EventStatus.IN_TRANSITION)
        setTransitionDialogOpen(true)
      else setTransitionDialogOpen(false)
    }
  }, [event, loading, mutationLoading, skipTransitionDialog])

  useEffect(() => {
    const interval = setInterval(() => {
      if (refetch) refetch()
    }, 2000)
    if (!waitingForJobStart) {
      clearInterval(interval)
    } else if (transitionDialogOpen) {
      setWaitingForJobStart(false)
      clearInterval(interval)
    }
    return () => clearInterval(interval)
  }, [transitionDialogOpen, refetch, waitingForJobStart])

  const handleSkipTransitionDialog = () => {
    setSkipTransitionDialog(true)
    setTransitionDialogOpen(false)
  }

  const onClick = async () => {
    try {
      const query = mutateQuery?.(event)
      await mutate(query, { returnPromise: true })
      setWaitingForJobStart(true)
    } catch (err) {
      notify((err as Error)?.message, 'error')
    }
  }

  return (
    <>
      <StatusTransitionDialog
        handleSkipDialog={handleSkipTransitionDialog}
        open={transitionDialogOpen}
        type={type}
      />
      <Button
        disabled={
          !hasAuthority(Authority.EDIT_EVENTS) ||
          type === EventStatus.IN_TRANSITION ||
          loading ||
          mutationLoading
        }
        className={classes.button}
        variant="contained"
        onClick={onClick}
        label={text}
        useSmallVersionBreakpoint={false}
      />
    </>
  )
}
