import { createStyles, makeStyles } from '@material-ui/core'
import React, { useState } from 'react'
import {
  sanitizeFetchType,
  useMutation,
  useRefresh,
  useShowContext,
} from 'react-admin'
import Button from '../../../common/customized-mui-components/Button'
import { ConfirmDialog } from '../PassageDialogs'
import { useHasAuthority } from '../../../hooks/useHasAuthority'
import { Authority } from '../../../../core/auth/Authority'
import { ResourceName } from '../../../../core/ResourceName'
import { PassageDto } from '../../../../core/dto/device/passages/passage.dto'

const useStyles = makeStyles(() =>
  createStyles({
    button: {
      margin: '2px',
    },
  }),
)

export const PassageRemoveTSCanCredentialsButton = () => {
  const { record: passage, loading } = useShowContext<PassageDto>()
  const classes = useStyles()
  const [
    removeTSCanCredentialsDialogOpen,
    setRemoveTSCanCredentialsDialogOpen,
  ] = useState(false)
  const hasAuthority = useHasAuthority()
  const [mutate, { loading: mutationLoading }] = useMutation()
  const refresh = useRefresh()

  const handleRemoveTSCanCredentials = async () => {
    setRemoveTSCanCredentialsDialogOpen(false)
    await mutate(
      {
        type: sanitizeFetchType('removeTSCanCredentials'),
        resource: ResourceName.PASSAGES,
        payload: {
          passageId: passage?.id,
        },
      },
      { returnPromise: true },
    )
    refresh()
  }

  const handleRemoveTSCanCredentialsDialog = () => {
    setRemoveTSCanCredentialsDialogOpen(true)
  }

  return (
    <>
      <ConfirmDialog
        open={removeTSCanCredentialsDialogOpen}
        confirm={handleRemoveTSCanCredentials}
        source="setTSCanCredentials"
        cancel={() => setRemoveTSCanCredentialsDialogOpen(false)}
        action="remove"
      />
      <Button
        variant="contained"
        onClick={handleRemoveTSCanCredentialsDialog}
        color="primary"
        className={classes.button}
        label="resources.passages.actions.removeTSCanCredentials"
        useSmallVersionBreakpoint={false}
        disabled={
          !hasAuthority(Authority.EDIT_PASSAGES) || loading || mutationLoading
        }
      />
    </>
  )
}
