import {
  useMediaQuery,
  useTheme,
  Grid,
  Divider,
  TableBody,
  Box,
  Typography,
} from '@material-ui/core'
import { Receipt } from '@material-ui/icons'
import React from 'react'
import {
  List,
  ListProps,
  Pagination,
  SelectInput,
  TextInput,
  useTranslate,
  Datagrid,
  TextField,
  DateField,
  Record,
  FunctionField,
  useLocale,
  DatagridBodyProps,
  DatagridRow,
  DatagridHeader,
  DatagridHeaderProps,
  ShowButton,
  Filter,
  ReferenceInput,
} from 'react-admin'
import { useHistory } from 'react-router'
import { getContrastColor } from '../../core/common/visual.utils'
import { ExtendedLog } from '../../core/dto/extended-log'
import { DateFilterInput } from './DateFilterInput'
import { VCenterGrid, VHCenterGrid } from './LogsComponents'
import FilterProps from '../common/FilterProps'
import { useHasAuthority } from '../hooks/useHasAuthority'
import { Authority } from '../../core/auth/Authority'
import { SelectWithPaginationInput } from '../common/SelectWithPaginationInput'
import { ResourceName } from '../../core/ResourceName'
import { OpenCameraPreviewDialogButton } from '../devices/camera/cameras/CameraPreviewDialog'

interface LogExpandProps {
  readonly noBooking?: boolean
  readonly noCamera?: boolean
  readonly record?: ExtendedLog
}

interface LogBodyProps extends DatagridBodyProps {
  readonly noBooking?: boolean
  readonly noCamera?: boolean
  readonly compactMode?: boolean
}

interface LogHeaderProps extends DatagridHeaderProps {
  readonly noBooking?: boolean
  readonly noCamera?: boolean
  readonly compactMode?: boolean
}

interface LogListProps extends ListProps {
  readonly compactMode?: boolean
  readonly noBooking?: boolean
  readonly noCamera?: boolean
}

const BookingButton = ({ style }: { style?: any }) => {
  const theme = useTheme()
  const xSmallScreen = useMediaQuery(theme.breakpoints.down('xs'))
  const history = useHistory()
  return (
    <FunctionField
      source="bookingId"
      render={(record: Record | undefined) =>
        record?.bookingId && (
          <>
            <ShowButton
              basePath="/bookings"
              label={record.bookingId.toString()}
              record={{ id: record.bookingId }}
              icon={<Receipt />}
              style={style}
            />
            {xSmallScreen && (
              <Typography
                color="primary"
                onClick={() =>
                  history.push(`/bookings/${record.bookingId}/show`)
                }
              >
                {record.bookingId}
              </Typography>
            )}
          </>
        )
      }
    />
  )
}

const LogExpand = ({ noBooking, noCamera, record }: LogExpandProps) => {
  const translate = useTranslate()
  return (
    <Box padding={1}>
      <Grid container spacing={1}>
        <VCenterGrid item xs={4}>
          {translate('resources.logs.fields.description')}:
        </VCenterGrid>
        <VCenterGrid item xs={8}>
          <TextField source="text" />
        </VCenterGrid>
        {!noBooking && record?.bookingId && (
          <>
            <Grid item xs={12}>
              <Divider />
            </Grid>
            <VCenterGrid item xs={4}>
              {translate('resources.logs.fields.bookingId')}:
            </VCenterGrid>
            <VHCenterGrid item xs={8}>
              <BookingButton />
            </VHCenterGrid>
          </>
        )}
        {!noCamera &&
          record?.cameraId !== null &&
          record?.cameraId !== undefined && (
            <>
              <Grid item xs={12}>
                <Divider />
              </Grid>
              <VCenterGrid item xs={4}>
                {translate('resources.logs.fields.cameraId')}:
              </VCenterGrid>
              <VHCenterGrid item xs={8}>
                <OpenCameraPreviewDialogButton
                  startDate={record.date}
                  cameraData={{
                    id: record?.cameraId,
                    description:
                      record?.cameraDescription !== null
                        ? record?.cameraDescription
                        : '',
                  }}
                  label=""
                  props={{ variant: undefined }}
                />
              </VHCenterGrid>
            </>
          )}
      </Grid>
    </Box>
  )
}

const LogBody = ({
  noBooking,
  noCamera,
  compactMode,
  data,
  ids,
  rowStyle,
  expand,
}: LogBodyProps) => {
  const hasAuthority = useHasAuthority()
  return (
    <TableBody>
      {data &&
        rowStyle &&
        ids?.map((id) => {
          const style = rowStyle(data[id], id as number)
          return (
            <DatagridRow
              key={id}
              id={id}
              record={data[id]}
              resource="logs"
              expand={expand}
              style={style}
            >
              <DateField
                source="date"
                style={style}
                options={{
                  hour: '2-digit',
                  minute: '2-digit',
                  second: '2-digit',
                }}
              />
              <TextField source="deviceName" style={style} />
              <TextField source="ticketCode" style={style} />
              {!compactMode && <TextField source="text" style={style} />}
              {!compactMode &&
                !noBooking &&
                (hasAuthority(Authority.VIEW_BOOKINGS) ? (
                  <BookingButton style={style} />
                ) : (
                  <TextField source="bookingId" />
                ))}
              {!compactMode &&
                !noCamera &&
                (hasAuthority(Authority.VIEW_CAMERAS) ? (
                  <FunctionField<ExtendedLog>
                    source="cameraId"
                    render={(record?: ExtendedLog) =>
                      record?.cameraId !== null &&
                      record?.cameraId !== undefined && (
                        <OpenCameraPreviewDialogButton
                          startDate={record.date}
                          cameraData={{
                            id: record?.cameraId,
                            description:
                              record?.cameraDescription !== null
                                ? record?.cameraDescription
                                : '',
                          }}
                          label=""
                          props={{ variant: undefined, style }}
                        />
                      )
                    }
                  />
                ) : (
                  <TextField source="cameraId" />
                ))}
            </DatagridRow>
          )
        })}
    </TableBody>
  )
}

const LogHeader = ({
  noBooking,
  noCamera,
  compactMode,
  ...props
}: LogHeaderProps) => (
  <DatagridHeader {...props}>
    <DateField source="date" label="resources.logs.fields.date" />
    <TextField source="deviceName" label="resources.logs.fields.deviceName" />
    <TextField source="ticketCode" label="resources.logs.fields.ticketCode" />
    {!compactMode && (
      <TextField
        source="text"
        sortable={false}
        label="resources.logs.fields.description"
      />
    )}
    {!noBooking && !compactMode && (
      <TextField source="bookingId" label="resources.logs.fields.bookingId" />
    )}
    {!noCamera && !compactMode && (
      <TextField source="cameraId" label="resources.logs.fields.cameraId" />
    )}
  </DatagridHeader>
)

const LogFilters = (props: FilterProps) => {
  const hasAuthority = useHasAuthority()
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))

  return (
    <Filter {...props}>
      <TextInput key="ticketCode" source="ticketCode" alwaysOn />
      <SelectInput
        key="deviceName"
        source="deviceName"
        choices={[
          {
            id: 'Czytnik',
            name: 'resources.logs.fields.deviceNameChoices.reader',
          },
          {
            id: 'Przej\u015Bcie',
            name: 'resources.logs.fields.deviceNameChoices.gateway',
          },
        ]}
      />
      <DateFilterInput source="date" key="date" />
      <TextInput source="bookingId" key="bookingId" />
      {hasAuthority(Authority.VIEW_OBJECTS) && (
        <ReferenceInput
          source="objectId"
          reference={ResourceName.OBJECTS}
          sort={{ field: 'name', order: 'ASC' }}
          perPage={smallScreen ? 5 : 15}
        >
          <SelectWithPaginationInput
            optionText="name"
            paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
          />
        </ReferenceInput>
      )}
      {hasAuthority(Authority.VIEW_EVENTS) && (
        <ReferenceInput
          label="resources.logs.fields.event"
          source="eventId"
          reference={ResourceName.EVENTS}
          sort={{ field: 'dateStart', order: 'DESC' }}
          perPage={smallScreen ? 5 : 15}
        >
          <SelectWithPaginationInput
            optionText="name"
            paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
          />
        </ReferenceInput>
      )}
    </Filter>
  )
}

export const LogList = ({
  compactMode = false,
  noBooking = false,
  noCamera = false,
  ...props
}: LogListProps) => {
  const { filter, ...listProps } = props
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const xSmallScreen = useMediaQuery(theme.breakpoints.down('xs'))
  const locale = useLocale()
  return (
    <List
      perPage={20}
      exporter={false}
      pagination={<Pagination rowsPerPageOptions={[20, 50, 100]} />}
      filters={<LogFilters />}
      filter={{ ...filter, locale }}
      sort={{ field: 'date', order: 'DESC' }}
      {...listProps}
      bulkActionButtons={false}
    >
      <Datagrid
        header={
          <LogHeader
            compactMode={smallScreen ? true : compactMode}
            noBooking={noBooking}
            noCamera={noCamera}
          />
        }
        body={
          <LogBody
            compactMode={smallScreen ? true : compactMode}
            noBooking={noBooking}
            noCamera={noCamera}
          />
        }
        isRowExpandable={() => smallScreen || compactMode}
        expand={<LogExpand noBooking={noBooking} noCamera={noCamera} />}
        rowStyle={(record: Record) => ({
          background: record && record.color,
          color:
            record && record.color !== 'transparent'
              ? getContrastColor(record.color)
              : 'inherit',
        })}
        padding={xSmallScreen ? 'none' : 'default'}
      >
        <div />
      </Datagrid>
    </List>
  )
}
