import { useMediaQuery, useTheme } from '@material-ui/core'
import React from 'react'
import {
  Datagrid,
  Filter,
  FunctionField,
  List,
  ListProps,
  Pagination,
  ReferenceField,
  ReferenceInput,
  SelectInput,
  TextField,
  TextInput,
  useListContext,
  useTranslate,
} from 'react-admin'
import { Authority } from '../../../core/auth/Authority'
import { PassageDto } from '../../../core/dto/device/passages/passage.dto'
import { HardwareVersions } from '../../../core/enum/HardwareVersions'
import { ResourceName } from '../../../core/ResourceName'
import FilterProps from '../../common/FilterProps'
import { SelectWithPaginationInput } from '../../common/SelectWithPaginationInput'
import { useHasAuthority } from '../../hooks/useHasAuthority'
import {
  CheckIfAnyModuleHasError,
  CheckLastRefresh,
  DeviceStatusesPreview,
} from '../common/modules/ModuleStatus'
import { SPOM4IpGateAddressField } from './SPOM4Address'

const PassageFilters = (props: FilterProps) => {
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const hasAuthority = useHasAuthority()

  return (
    <Filter {...props}>
      <TextInput source="name" alwaysOn />
      {hasAuthority(Authority.VIEW_DEVICE_CATEGORIES) && (
        <ReferenceInput
          label="resources.palms.fields.categoryId"
          source="categoryIdWithDescendants"
          reference={ResourceName.DEVICE_CATEGORIES}
          sort={{ field: 'name', order: 'ASC' }}
          perPage={smallScreen ? 5 : 15}
        >
          <SelectWithPaginationInput
            optionText="name"
            paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
          />
        </ReferenceInput>
      )}
      <SelectInput
        source="hardwareVersion"
        choices={Object.entries(HardwareVersions).map(([value]) => ({
          id: value,
          name: `resources.enums.hardwareVersions.${value}`,
        }))}
      />
    </Filter>
  )
}

const PassageGridExpand = ({ ...props }) => {
  const { record } = props
  return <DeviceStatusesPreview jsonStatus={record.jsonStatus} />
}

const PassagesGrid = ({ ...props }) => {
  const { loading } = useListContext()
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const hasAuthority = useHasAuthority()
  const translate = useTranslate()
  const postRowStyle = (record: any) => {
    const passageError = CheckLastRefresh(record.lastRefresh)
    let passageWarning = false
    if (!passageError) {
      // Check for modules error
      passageWarning = CheckIfAnyModuleHasError(record.jsonStatus)
    }
    if (passageError) {
      return {
        backgroundColor: theme.palette.error.main,
        color: theme.palette.error.contrastText,
      }
    }
    if (passageWarning) {
      return {
        backgroundColor: theme.palette.warning.main,
      }
    }
    return undefined
  }

  return (
    <Datagrid
      {...props}
      rowStyle={postRowStyle}
      size="small"
      loaded={!loading}
      rowClick={(id) => `${ResourceName.PASSAGES}/${id}/show`}
      isRowExpandable={(record) =>
        record?.jsonStatus !== undefined && record?.jsonStatus !== null
      }
      expand={<PassageGridExpand />}
    >
      <TextField source="id" label="ID" />
      <TextField source="name" />
      {!smallScreen && (
        <FunctionField<PassageDto>
          source="address"
          render={(record?: PassageDto) =>
            record?.hardwareVersion === HardwareVersions.SPOM4 ? (
              <SPOM4IpGateAddressField source="address" record={record} />
            ) : (
              <TextField source="address" />
            )
          }
        />
      )}
      {!smallScreen && (
        <FunctionField<PassageDto>
          source="hardwareVersion"
          render={(record?: PassageDto) => (
            <div>
              {translate(
                `resources.enums.hardwareVersions.${record?.hardwareVersion}`,
              )}
            </div>
          )}
        />
      )}
      {hasAuthority(Authority.VIEW_DEVICE_CATEGORIES) && (
        <ReferenceField
          source="categoryId"
          reference={ResourceName.DEVICE_CATEGORIES}
          link={false}
        >
          <TextField source="name" />
        </ReferenceField>
      )}
    </Datagrid>
  )
}

export const PassagesList = ({ ...props }: ListProps) => (
  <List
    {...props}
    perPage={20}
    pagination={<Pagination rowsPerPageOptions={[10, 20, 50]} />}
    exporter={false}
    filters={<PassageFilters />}
    sort={{
      field: 'id',
      order: 'DESC',
    }}
    bulkActionButtons={false}
  >
    <PassagesGrid />
  </List>
)
