import {
  createStyles,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  IconButton,
  makeStyles,
  Theme,
} from '@material-ui/core'
import { Close } from '@material-ui/icons'
import React, { useEffect } from 'react'
import { useShowContext, useTranslate } from 'react-admin'
import { EventStatus } from '../../../core/enum/EventStatus'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    actions: {
      padding: 0,
      background: 'rgba(0,0,0,0.2)',
    },
    button: {
      borderRadius: 0,
      background: theme.palette.primary.main,
    },
    title: {
      padding: '5px 24px',
    },
  }),
)

export const StatusTransitionDialog = ({
  handleSkipDialog,
  open,
  type,
}: {
  handleSkipDialog: () => void
  open: boolean
  type: EventStatus
}) => {
  const classes = useStyles()
  const translate = useTranslate()
  const { refetch } = useShowContext()

  useEffect(() => {
    const interval = setInterval(() => {
      if (refetch) refetch()
    }, 5000)

    if (!open) {
      clearInterval(interval)
    }

    return () => clearInterval(interval)
  }, [open, refetch, type])

  return (
    <Dialog open={open}>
      <DialogActions className={classes.actions}>
        <IconButton
          size="small"
          className={classes.button}
          onClick={handleSkipDialog}
        >
          <Close fontSize="small" />
        </IconButton>
      </DialogActions>
      <DialogTitle className={classes.title}>
        {translate('resources.events.dialogs.transition.title')}
      </DialogTitle>
      <DialogContent>
        <DialogContentText>
          {translate('resources.events.dialogs.transition.content')}
        </DialogContentText>
      </DialogContent>
    </Dialog>
  )
}
