import {
  createStyles,
  Dialog,
  DialogActions,
  DialogContent,
  IconButton,
  makeStyles,
  Paper,
  Typography,
} from '@material-ui/core'
import { Close, Videocam } from '@material-ui/icons'
import React, { useCallback, useEffect, useState } from 'react'
import { useMutation, useNotify, useShowContext } from 'react-admin'
import Draggable from 'react-draggable'
import ReactHlsPlayer from 'react-hls-player/dist'
import { Authority } from '../../../../core/auth/Authority'
import { DateTime } from '../../../../core/common/date-time'
import { CameraDto } from '../../../../core/dto/device/camera/cameras/camera.dto'
import { ResourceName } from '../../../../core/ResourceName'
import Button, {
  ButtonProps,
} from '../../../common/customized-mui-components/Button'
import { useHasAuthority } from '../../../hooks/useHasAuthority'

const useStyles = makeStyles(() =>
  createStyles({
    actions: {
      padding: 0,
      background: 'rgba(0,0,0,0.2)',
    },
    title: {
      marginLeft: '10px',
      marginRight: 'auto',
    },
    button: {
      margin: '2px',
    },
    video: {
      maxWidth: '-webkit-fill-available',
      maxHeight: '500px',
    },
    dialogContent: {
      padding: 0,
    },
  }),
)

function DraggableComponent({ ...props }) {
  const nodeRef = React.useRef(null)
  return (
    <Draggable
      nodeRef={nodeRef}
      handle="#draggable-dialog-title"
      cancel={'[class*="MuiDialogContent-root"]'}
    >
      <Paper ref={nodeRef} {...props} />
    </Draggable>
  )
}

export const CameraPreviewDialog = ({
  open,
  close,
  startDate,
  cameraData,
}: {
  open: boolean
  close: () => void
  startDate?: DateTime
  cameraData?: CameraPreviewData
}) => {
  let camera: CameraDto | CameraPreviewData | undefined
  const { record } = useShowContext<CameraDto>()
  if (cameraData) camera = cameraData
  else camera = record
  const [mutate] = useMutation()
  const playerRef = React.useRef<HTMLVideoElement>(null)
  const [jobId, setJobId] = useState(undefined)
  const [streamName, setStreamName] = useState('')
  const [showPlayer, setShowPlayer] = useState(true)
  const [streamLoaded, setStreamLoaded] = useState(false)
  const [maxRetriesLeft, setMaxRetriesLeft] = useState(15)
  const classes = useStyles()
  const notify = useNotify()

  const openStream = useCallback(async () => {
    const streamInfo = await mutate(
      {
        type: 'openStream',
        resource: ResourceName.CAMERAS,
        payload: {
          cameraId: camera?.id,
          streamStart: startDate,
        },
      },
      { returnPromise: true },
    )
    setStreamName(streamInfo.data.streamFile)
    setJobId(streamInfo.data.jobId)
  }, [mutate, camera, startDate, setStreamName, setJobId])

  const closeStream = useCallback(async () => {
    if (jobId)
      await mutate(
        {
          type: 'deleteJob',
          resource: ResourceName.JOBS,
          payload: {
            jobId,
          },
        },
        { returnPromise: true },
      )
  }, [mutate, jobId])

  const handleClose = useCallback(() => {
    close()
  }, [close])

  const reload = () => {
    setShowPlayer(false)
  }

  const onHlsDataLoaded = () => {
    setStreamLoaded(true)
  }

  const handleTimeout = useCallback(() => {
    handleClose()
    notify('resources.cameras.dialogs.preview.streamTimeout')
  }, [handleClose, notify])

  useEffect(() => {
    const interval = setInterval(() => {
      reload()
      setMaxRetriesLeft(maxRetriesLeft - 1)
    }, 2000)
    if (streamLoaded || !open) {
      clearInterval(interval)
    } else if (maxRetriesLeft <= 0) {
      handleTimeout()
    }
    return () => clearInterval(interval)
  }, [streamLoaded, open, maxRetriesLeft, handleTimeout])

  useEffect(() => {
    if (!showPlayer) setShowPlayer(true)
  }, [showPlayer])

  useEffect(() => {
    playerRef.current?.addEventListener('loadeddata', onHlsDataLoaded)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [playerRef.current])

  useEffect(() => {
    if (open) {
      openStream()
    }
  }, [open, openStream])

  useEffect(() => {
    if (!open) {
      setStreamLoaded(false)
      setStreamName('')
      closeStream()
      setJobId(undefined)
      setMaxRetriesLeft(15)
    }
  }, [open, closeStream])

  return (
    <Dialog
      open={open}
      onClose={handleClose}
      PaperComponent={DraggableComponent}
      aria-labelledby="draggable-dialog-title"
      maxWidth="md"
    >
      <DialogActions
        className={classes.actions}
        style={{ cursor: 'move' }}
        id="draggable-dialog-title"
      >
        <Typography className={classes.title}>{camera?.description}</Typography>
        <IconButton
          size="small"
          className={classes.button}
          onClick={handleClose}
        >
          <Close fontSize="small" />
        </IconButton>
      </DialogActions>
      <DialogContent className={classes.dialogContent}>
        {showPlayer && (
          <ReactHlsPlayer
            className={classes.video}
            playerRef={playerRef}
            src={`streams/${streamName}`}
            autoPlay
            muted
            controls
            width="auto"
            height="auto"
          />
        )}
      </DialogContent>
    </Dialog>
  )
}

export const OpenCameraPreviewDialogButton = ({
  label,
  startDate,
  cameraData,
  props,
}: {
  label?: string
  startDate?: DateTime
  cameraData?: CameraPreviewData
  props?: ButtonProps
}) => {
  const classes = useStyles()
  const [dialogOpen, setDialogOpen] = useState(false)
  const hasAuthority = useHasAuthority()

  const onClose = () => {
    setDialogOpen(false)
  }

  const openDialog = () => {
    setDialogOpen(true)
  }

  return (
    <>
      <CameraPreviewDialog
        open={dialogOpen}
        close={onClose}
        startDate={startDate}
        cameraData={cameraData}
      />
      <Button
        variant="contained"
        color="primary"
        className={classes.button}
        label={
          label == null ? 'resources.cameras.actions.openCameraPreview' : label
        }
        useSmallVersionBreakpoint={false}
        disabled={!hasAuthority(Authority.VIEW_CAMERAS)}
        {...props}
        onClick={openDialog}
      >
        <Videocam />
      </Button>
    </>
  )
}

export interface CameraPreviewData {
  readonly id: number
  readonly description: string
}
