const MultiModuleStatus = (props: MultiModuleStatusProps) => {
  const classes = useStyles()
  const { moduleInfo } = props

  return (
    <>
      {moduleInfo.map((record) => (
        <Accordion key={record.id}>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            className={
              LastRefreshError(record.lastRefresh)
                ? classes.terminatedModuleHeader
                : classes.defaultModuleHeader
            }
          >
            <RecordContextProvider value={record}>
              <TextField variant="h6" source="module.alias" />
            </RecordContextProvider>
          </AccordionSummary>
          <AccordionDetails
            className={
              LastRefreshError(record.lastRefresh)
                ? classes.terminatedModuleDetails
                : classes.defaultModuleDetails
            }
          >
            <ModuleStatus moduleInfo={record} disableAliasHeader />
          </AccordionDetails>
        </Accordion>
      ))}
    </>
  )
}

const DeviceStatusesTab = (props: StatusTabProps) => {
  const { jsonStatus: rawJsonStatus } = props
  let jsonStatus
  try {
    jsonStatus = JSON.parse(rawJsonStatus) as Array<any>
    if (jsonStatus === null || jsonStatus === undefined) jsonStatus = []
  } catch {
    jsonStatus = []
  }
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const modules = Object.entries(jsonStatus).map(([key, value]) =>
    moduleMapper(camelcaseKeys(value, { deep: true }), key),
  )

  return (
    <MultiModuleStatus
      moduleInfo={modules
        .filter((m) => m != null)
        .map((m) => m as ModuleBaseDto)}
    />
  )
}

const DeviceStatusesPreview = (props: StatusTabProps) => {
  const classes = useStyles()
  const { jsonStatus: rawJsonStatus } = props
  let jsonStatus
  try {
    jsonStatus = JSON.parse(rawJsonStatus) as Array<any>
  } catch {
    jsonStatus = []
  }
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const modules = Object.entries(jsonStatus)
    .map(([key, value]) =>
      moduleMapper(camelcaseKeys(value, { deep: true }), key),
    )
    .filter((m) => m != null)
    .map((m) => m as ModuleBaseDto)

  return (
    <Grid
      container
      direction="row"
      justify="flex-start"
      alignItems="flex-start"
    >
      {modules.map((module, index) => (
        // eslint-disable-next-line react/no-array-index-key
        <Grid item key={index} style={{ margin: 3 }}>
          <Box
            p={1}
            className={
              LastRefreshError(module.lastRefresh)
                ? classes.previewedDeviceError
                : classes.previewedDeviceOk
            }
          >
            <TextField source="module.alias" record={module} />
          </Box>
        </Grid>
      ))}
    </Grid>
  )
}

const CheckIfAnyModuleHasError = (rawJsonStatus: string) => {
  let jsonStatus
  try {
    jsonStatus = JSON.parse(rawJsonStatus) as Array<any>
  } catch {
    jsonStatus = []
  }
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const modules = Object.entries(jsonStatus)
    .map(([key, value]) =>
      moduleMapper(camelcaseKeys(value, { deep: true }), key),
    )
    .filter((m) => m != null)
    .map((m) => m as ModuleBaseDto)

  return modules.some((module) => LastRefreshError(module.lastRefresh))
}

interface ModuleBaseStatusProps {
  readonly moduleInfo: ModuleBaseDto
  readonly disableAliasHeader?: boolean
  readonly children?: ReactNode[]
}

interface ModuleStatusProps {
  readonly moduleInfo: ModuleBaseDto
  readonly disableAliasHeader?: boolean
}

interface MultiModuleStatusProps {
  readonly moduleInfo: ModuleBaseDto[]
}

interface StatusTabProps {
  readonly jsonStatus: string
}

export {
  LastRefreshError as CheckLastRefresh,
  ModuleStatus,
  MultiModuleStatus,
  DeviceStatusesTab,
  DeviceStatusesPreview,
  CheckIfAnyModuleHasError,
}
