import { Box } from '@material-ui/core'
import { useListContext } from 'ra-core'
import React from 'react'
import {
  FilterButton,
  FunctionField,
  List,
  ListActionsProps,
  ListProps,
  Pagination,
  required,
  SelectInput,
  TextField,
  TextInput,
  TopToolbar,
  useTranslate,
} from 'react-admin'
import { BlockDto } from '../../../core/dto/block.dto'
import { BlockType } from '../../../core/enum/BlockType'
import {
  EditableDatagrid,
  RowForm,
} from '../../../lib/@react-admin/ra-editable-datagrid'
import ParameterizedCreateButton from '../../common/ParameterizedCreateButton'
import Empty from '../Empty'

const ListFilters = () => [
  <TextInput source="name" alwaysOn />,
  <TextInput source="id" />,
  <TextInput source="tribuneId" />,
  <TextInput source="code" />,
]

const EditForm = ({ ...props }) => (
  <RowForm {...props}>
    <TextField source="id" />
    <TextInput source="name" validate={required()} />
    <TextField source="tribuneId" />
    <SelectInput
      source="type"
      choices={Object.entries(BlockType).map(([value]) => ({
        id: value,
        name: `resources.enums.blockType.${value}`,
      }))}
    />
    <TextInput source="code" />
  </RowForm>
)

const BlocksGrid = ({ ...props }) => {
  const translate = useTranslate()

  return (
    <EditableDatagrid {...props} size="small" editForm={<EditForm />} noDelete>
      <TextField source="id" />
      <TextField source="name" />
      <TextField source="tribuneId" />
      <FunctionField<BlockDto>
        source="type"
        render={(r?: BlockDto) =>
          translate(`resources.enums.blockType.${r?.type}`)
        }
      />
      <TextField source="code" />
    </EditableDatagrid>
  )
}

interface BlockListActions extends ListActionsProps {
  objectId?: number
}

const ListActions = ({ objectId }: BlockListActions) => (
  <TopToolbar>
    <FilterButton />
    <ParameterizedCreateButton searchParams={{ objectId }} />
  </TopToolbar>
)

const EmptyView = ({ objectId }: { objectId?: number }) => {
  const translate = useTranslate()
  return (
    <Box>
      <Empty text={translate('resources.blocks.empty')} />
      <TopToolbar>
        <ParameterizedCreateButton searchParams={{ objectId }} />
      </TopToolbar>
    </Box>
  )
}

interface BlockListProps extends ListProps {
  objectId?: number
}

const BlocksList = ({ objectId, ...props }: BlockListProps) => {
  const { basePath } = useListContext()

  return (
    <List
      {...props}
      basePath={basePath}
      exporter={false}
      filters={ListFilters()}
      actions={<ListActions objectId={objectId} />}
      bulkActionButtons={false}
      perPage={20}
      pagination={<Pagination rowsPerPageOptions={[10, 20, 50]} />}
      empty={<EmptyView objectId={objectId} />}
    >
      <BlocksGrid />
    </List>
  )
}

export default BlocksList
