import { useMediaQuery, useTheme } from '@material-ui/core'
import { Cancel } from '@material-ui/icons'
import React from 'react'
import {
  Create,
  CreateProps,
  useTranslate,
  SimpleForm,
  TextInput,
  SaveButton,
  ListButton,
  Toolbar,
  ToolbarProps,
  BooleanInput,
  ReferenceInput,
  SelectInput,
} from 'react-admin'
import { Authority } from '../../../core/auth/Authority'
import { DetectorDto } from '../../../core/dto/device/detector/detector.dto'
import { HardwareVersions } from '../../../core/enum/HardwareVersions'
import { ResourceName } from '../../../core/ResourceName'
import { SelectWithPaginationInput } from '../../common/SelectWithPaginationInput'
import { useHasAuthority } from '../../hooks/useHasAuthority'

const validateDetectorCreate = (values: DetectorDto) => {
  const errors: { [n: string]: string } = {}
  if (!values.name) {
    errors.name = 'ra.validation.required'
  }
  if (!values.address) {
    errors.address = 'ra.validation.required'
  }
  return errors
}

const CreateTitle = () => {
  const translate = useTranslate()
  return <span>{translate('resources.detectors.titles.create')}</span>
}

const CreateToolbar = (props: ToolbarProps) => (
  <Toolbar {...props}>
    <SaveButton />
    <ListButton icon={<Cancel />} label="const.cancel" />
  </Toolbar>
)

const Detector = ({ ...props }) => {
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const hasAuthority = useHasAuthority()

  return (
    <SimpleForm
      {...props}
      redirect="show"
      toolbar={<CreateToolbar />}
      validate={validateDetectorCreate}
    >
      <TextInput source="name" isRequired />
      <BooleanInput source="isWatched" />
      <TextInput source="address" isRequired />
      {hasAuthority(Authority.VIEW_DEVICE_CATEGORIES) && (
        <ReferenceInput
          source="categoryId"
          reference={ResourceName.DEVICE_CATEGORIES}
          sort={{ field: 'name', order: 'ASC' }}
          perPage={smallScreen ? 5 : 15}
        >
          <SelectWithPaginationInput
            optionText="name"
            paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
          />
        </ReferenceInput>
      )}
      <SelectInput
        source="hardwareVersion"
        defaultValue={HardwareVersions.MODULO5}
        choices={Object.entries(HardwareVersions).map(([value]) => ({
          id: value,
          name: `resources.enums.hardwareVersions.${value}`,
        }))}
      />
    </SimpleForm>
  )
}

export const DetectorCreate = (props: CreateProps) => (
  <Create {...props} hasShow={false} title={<CreateTitle />}>
    <Detector />
  </Create>
)
