import { AuthProvider } from 'react-admin'
import { get } from '../common/fetch.utils'
import { RoleDto } from '../dto/role/role.dto'
import { ROLES_URL } from '../api-urls'

const areRolesOutOfSync = (local: Set<string>, remote: string[]) =>
  local.size !== remote.length || !remote.every((value) => local.has(value))

const validateRoles = async (
  dataProvider: AuthProvider,
  params: any,
  rolesFromRemote: string[],
): Promise<Set<string>> => {
  const rolesFromLocalStorage = (await dataProvider.getPermissions(
    params,
  )) as Set<string>
  if (areRolesOutOfSync(rolesFromLocalStorage, rolesFromRemote)) {
    // eslint-disable-next-line no-console
    console.error('Corrupted permissions')
    await dataProvider.logout('')
    return Promise.resolve(new Set([]))
  }
  return Promise.resolve(rolesFromLocalStorage)
}

export const withSynchronizedPermissions = (dataProvider: AuthProvider) => {
  // this is set once on app initialization
  let rolesFromRemote: string[] | null = null

  return new Proxy<AuthProvider>(dataProvider, {
    get:
      (target, name: string) =>
      async (params: any): Promise<any> => {
        switch (name as keyof AuthProvider) {
          // avoid re-executing getRoles call
          case 'login': {
            await dataProvider.login(params)
            const rolesFromLocalStorage = (await dataProvider.getPermissions(
              params,
            )) as Set<string>
            rolesFromRemote = Array.from(rolesFromLocalStorage)
            return Promise.resolve()
          }
          case 'getPermissions': {
            if (rolesFromRemote) {
              // compares the status of local and remote roles each time it checks for permissions
              return validateRoles(dataProvider, params, rolesFromRemote)
            }
            // after reloading the page it needs to fetch roles from remote
            const roleDtos = await get<RoleDto[]>(ROLES_URL)
            const roles = roleDtos.map((v) => v.name)
            rolesFromRemote = roles
            localStorage.setItem('roles', roles.join())
            return Promise.resolve(new Set(roles))
          }
          default: {
            return dataProvider[name](params)
          }
        }
      },
  })
}
