import { useMediaQuery, useTheme } from '@material-ui/core'
import React from 'react'
import {
  Datagrid,
  List,
  ListProps,
  Pagination,
  TextField,
  TextInput,
  useListContext,
  Filter,
  ReferenceField,
  ReferenceInput,
} from 'react-admin'
import { Authority } from '../../../core/auth/Authority'
import { ResourceName } from '../../../core/ResourceName'
import FilterProps from '../../common/FilterProps'
import { SelectWithPaginationInput } from '../../common/SelectWithPaginationInput'
import { useHasAuthority } from '../../hooks/useHasAuthority'

const UsersFilters = (props: FilterProps) => {
  const hasAuthority = useHasAuthority()
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))

  return (
    <Filter {...props}>
      <TextInput source="userName" alwaysOn />
      {hasAuthority(Authority.VIEW_COMPANIES) && (
        <ReferenceInput
          source="companyId"
          reference={ResourceName.COMPANIES}
          sort={{ field: 'id', order: 'ASC' }}
          perPage={smallScreen ? 5 : 15}
        >
          <SelectWithPaginationInput
            optionText="name"
            paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
          />
        </ReferenceInput>
      )}
    </Filter>
  )
}

const UsersGrid = () => {
  const { loading } = useListContext()
  const hasAuthority = useHasAuthority()

  return (
    <Datagrid
      size="small"
      loaded={!loading}
      rowClick={(id) => `${ResourceName.USERS}/${id}/show`}
    >
      <TextField source="id" label="ID" />
      <TextField source="userName" />
      {hasAuthority(Authority.VIEW_COMPANIES) && (
        <ReferenceField
          source="companyId"
          reference={ResourceName.COMPANIES}
          link="show"
        >
          <TextField source="name" />
        </ReferenceField>
      )}
    </Datagrid>
  )
}

export const UserList = ({ ...props }: ListProps) => (
  <List
    {...props}
    perPage={20}
    pagination={<Pagination rowsPerPageOptions={[10, 20, 50]} />}
    exporter={false}
    filters={<UsersFilters />}
  >
    <UsersGrid />
  </List>
)
