import { createStyles, makeStyles } from '@material-ui/core'
import React, { useState } from 'react'
import {
  sanitizeFetchType,
  useMutation,
  useRefresh,
  useShowContext,
} from 'react-admin'
import { Authority } from '../../../../../core/auth/Authority'
import { CameraServerDto } from '../../../../../core/dto/device/camera/camera-servers/camera-server.dto'
import { ResourceName } from '../../../../../core/ResourceName'
import Button from '../../../../common/customized-mui-components/Button'
import { useHasAuthority } from '../../../../hooks/useHasAuthority'
import { ConfirmDialog } from '../CameraServerDialogs'

const useStyles = makeStyles(() =>
  createStyles({
    button: {
      margin: '2px',
    },
  }),
)

export const CameraServerRemoveCredentialsButton = () => {
  const { record: cameraServer, loading } = useShowContext<CameraServerDto>()
  const classes = useStyles()
  const [removeCredentialsDialogOpen, setRemoveCredentialsDialogOpen] =
    useState(false)
  const hasAuthority = useHasAuthority()
  const [mutate, { loading: mutationLoading }] = useMutation()
  const refresh = useRefresh()

  const handleRemoveCredentials = async () => {
    setRemoveCredentialsDialogOpen(false)
    await mutate(
      {
        type: sanitizeFetchType('removeCredentials'),
        resource: ResourceName.CAMERA_SERVERS,
        payload: {
          cameraServerId: cameraServer?.id,
        },
      },
      { returnPromise: true },
    )
    refresh()
  }

  const handleRemoveCredentialsDialog = () => {
    setRemoveCredentialsDialogOpen(true)
  }

  return (
    <>
      <ConfirmDialog
        open={removeCredentialsDialogOpen}
        confirm={handleRemoveCredentials}
        source="setCredentials"
        cancel={() => setRemoveCredentialsDialogOpen(false)}
        action="remove"
      />
      <Button
        variant="contained"
        onClick={handleRemoveCredentialsDialog}
        color="primary"
        className={classes.button}
        label="resources.camera-servers.actions.removeCredentials"
        useSmallVersionBreakpoint={false}
        disabled={
          !hasAuthority(Authority.EDIT_CAMERA_SERVERS) ||
          loading ||
          mutationLoading
        }
      />
    </>
  )
}
