import { useMediaQuery, useTheme } from '@material-ui/core'
import { Cancel } from '@material-ui/icons'
import React from 'react'
import {
  BooleanInput,
  DateField,
  Edit,
  EditProps,
  ReferenceInput,
  SaveButton,
  ShowButton,
  SimpleForm,
  TextField,
  TextInput,
  TitleProps,
  Toolbar,
  ToolbarProps,
  useTranslate,
} from 'react-admin'
import { Authority } from '../../../core/auth/Authority'
import { OfflineServerDto } from '../../../core/dto/device/offline-servers/offline-server.dto'
import { ResourceName } from '../../../core/ResourceName'
import { SelectWithPaginationInput } from '../../common/SelectWithPaginationInput'
import { useHasAuthority } from '../../hooks/useHasAuthority'

const validateEngineEdit = (values: OfflineServerDto) => {
  const errors: { [n: string]: string } = {}
  if (!values.name) {
    errors.name = 'ra.validation.required'
  }
  if (!values.address) {
    errors.address = 'ra.validation.required'
  }
  return errors
}

interface OfflineServerTitleProps extends TitleProps {
  record?: OfflineServerDto
}

const EditTitle = (data: OfflineServerTitleProps) => {
  const { record } = data
  const translate = useTranslate()
  return (
    <span>
      {translate('resources.offline-servers.titles.edit')}: {record?.id}.{' '}
      {record?.name}{' '}
    </span>
  )
}

const EditToolbar = (props: ToolbarProps) => (
  <Toolbar {...props}>
    <SaveButton />
    <ShowButton icon={<Cancel />} label="const.cancel" />
  </Toolbar>
)

const OfflineServer = ({ ...props }) => {
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const hasAuthority = useHasAuthority()

  return (
    <SimpleForm
      {...props}
      redirect="show"
      toolbar={<EditToolbar />}
      validate={validateEngineEdit}
    >
      <TextField source="id" label="ID" />
      <TextInput source="name" isRequired />
      <BooleanInput source="isWatched" />
      <DateField
        source="lastRefresh"
        showTime
        options={{
          day: '2-digit',
          month: '2-digit',
          year: 'numeric',
          hour: '2-digit',
          minute: '2-digit',
          second: '2-digit',
        }}
      />
      <TextInput source="address" isRequired />
      {hasAuthority(Authority.VIEW_DEVICE_CATEGORIES) && (
        <ReferenceInput
          source="categoryId"
          reference={ResourceName.DEVICE_CATEGORIES}
          sort={{ field: 'name', order: 'ASC' }}
          perPage={smallScreen ? 5 : 15}
        >
          <SelectWithPaginationInput
            optionText="name"
            paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
          />
        </ReferenceInput>
      )}
      {hasAuthority(Authority.VIEW_CAMERAS) && (
        <ReferenceInput
          source="cameraId"
          reference={ResourceName.CAMERAS}
          sort={{ field: 'description', order: 'ASC' }}
          perPage={smallScreen ? 5 : 15}
        >
          <SelectWithPaginationInput
            optionText="description"
            paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
          />
        </ReferenceInput>
      )}
    </SimpleForm>
  )
}

export const OfflineServerEdit = (props: EditProps) => (
  <Edit {...props} hasShow={false} title={<EditTitle />}>
    <OfflineServer />
  </Edit>
)
