import {
  CREATE,
  CreateParams,
  sanitizeFetchType,
  UPDATE_MANY,
  UpdateManyParams,
} from 'react-admin'
import { ResourceName } from '../ResourceName'
import { AccessUsingsCreateParams } from '../access-usings/access-usings-create.params'
import {
  ToggleType,
  ToggleButtonAction,
  ToggleButtonState,
} from './toggle-booking-blockade-button'
import { VariantDto } from '../dto/variant.dto'
import { AccessUsingDto } from '../dto/access-using.dto'
import { BookingDto } from '../dto/booking.dto'

type Mutation = {
  type: string
  resource: string
  // eslint-disable-next-line @typescript-eslint/ban-types
  payload?: object
}

const blockMutation = (
  ticketDetails?: BookingDto,
  variant?: VariantDto,
): Mutation => ({
  type: sanitizeFetchType(CREATE),
  resource: ResourceName.ACCESS_USINGS,
  payload: {
    data: {
      ticketIds: ticketDetails?.ticketsIds,
      accessIds: variant?.accessesIds,
    },
  } as CreateParams<AccessUsingsCreateParams>,
})

const unblockMutation = (
  ticketDetails?: BookingDto,
  variant?: VariantDto,
  accessUsings?: AccessUsingDto[],
): Mutation => {
  const activeAccessUsingIds = accessUsings
    ?.filter((au) => au.active)
    .map((au) => au.id)

  return {
    type: sanitizeFetchType(UPDATE_MANY),
    resource: ResourceName.ACCESS_USINGS,
    payload: {
      ids: activeAccessUsingIds,
      data: {
        active: false,
      },
    } as UpdateManyParams,
  }
}

type State = {
  mutateQuery?: (
    ticketDetails?: BookingDto,
    variant?: VariantDto,
    accessUsings?: AccessUsingDto[],
  ) => Mutation
} & ToggleButtonState

export const blockAccessButtonInitialState = {
  type: ToggleType.DISABLED,
  text: 'resources.bookings.actions.blockAccessNA',
}

export const toggleAccessButtonReducer = (
  state: State,
  { type }: ToggleButtonAction,
): State => {
  switch (type) {
    case ToggleType.BLOCK: {
      return {
        type,
        text: 'resources.bookings.actions.blockAccess',
        mutateQuery: blockMutation,
      }
    }
    case ToggleType.UNBLOCK: {
      return {
        type,
        text: 'resources.bookings.actions.unblockAccess',
        mutateQuery: unblockMutation,
      }
    }
    case ToggleType.DISABLED: {
      return blockAccessButtonInitialState
    }
    default:
      throw new Error()
  }
}
