import { Cancel } from '@material-ui/icons'
import React from 'react'
import {
  Create,
  CreateProps,
  useTranslate,
  SimpleForm,
  TextInput,
  SaveButton,
  ListButton,
  Toolbar,
  ToolbarProps,
} from 'react-admin'
import { CameraServerDto } from '../../../../core/dto/device/camera/camera-servers/camera-server.dto'

const validateCameraServerCreate = (values: CameraServerDto) => {
  const errors: { [n: string]: string } = {}
  if (!values.name) {
    errors.name = 'ra.validation.required'
  }
  if (!values.address) {
    errors.address = 'ra.validation.required'
  }
  if (!values.serverId) {
    errors.serverId = 'ra.validation.required'
  }
  if (!values.delay) {
    errors.delay = 'ra.validation.required'
  }
  return errors
}

const CreateTitle = () => {
  const translate = useTranslate()
  return <span>{translate('resources.camera-servers.titles.create')}</span>
}

const CreateToolbar = (props: ToolbarProps) => (
  <Toolbar {...props}>
    <SaveButton />
    <ListButton icon={<Cancel />} label="const.cancel" />
  </Toolbar>
)

const CameraServer = ({ ...props }) => (
  <SimpleForm
    {...props}
    redirect="show"
    toolbar={<CreateToolbar />}
    validate={validateCameraServerCreate}
  >
    <TextInput source="name" isRequired />
    <TextInput source="address" isRequired />
    <TextInput source="serverId" isRequired />
    <TextInput source="delay" isRequired />
  </SimpleForm>
)

export const CameraServerCreate = (props: CreateProps) => (
  <Create {...props} hasShow={false} title={<CreateTitle />}>
    <CameraServer />
  </Create>
)
