import {
  createStyles,
  IconButton,
  makeStyles,
  useMediaQuery,
  useTheme,
} from '@material-ui/core'
import { Power, PowerOff } from '@material-ui/icons'
import React, { useReducer, useEffect } from 'react'
import { useMutation, useNotify, useRefresh, useShowContext } from 'react-admin'
import {
  activationButtonInitialState,
  toggleActivationButtonReducer,
} from '../../../core/access-usings/access-using-activation-button.reducer'
import { ToggleType } from '../../../core/access-usings/access-using-activation-button'
import Button from '../../common/customized-mui-components/Button'
import { useHasAuthority } from '../../hooks/useHasAuthority'
import { Authority } from '../../../core/auth/Authority'

const useStyles = makeStyles(() =>
  createStyles({
    button: {
      margin: '2px',
      width: '100%',
    },
  }),
)

export const AccessUsingActivationButton = ({ ...props }) => {
  const classes = useStyles()
  const theme = useTheme()
  const xSmallScreen = useMediaQuery(theme.breakpoints.down('xs'))
  const notify = useNotify()
  const hasAuthority = useHasAuthority()
  const { loading } = useShowContext()
  const [{ type, text, mutateQuery }, dispatch] = useReducer(
    toggleActivationButtonReducer,
    activationButtonInitialState,
  )
  const { record: accessUsing } = props
  const [mutate, { loading: mutationLoading }] = useMutation()
  const refresh = useRefresh()

  useEffect(() => {
    if (accessUsing.active) {
      dispatch({ type: ToggleType.ACTIVE })
    } else {
      dispatch({ type: ToggleType.INACTIVE })
    }
  }, [accessUsing])

  const onClick = async () => {
    try {
      const query = mutateQuery?.(accessUsing)
      await mutate(query, { returnPromise: true })
      refresh()
    } catch (err) {
      notify((err as Error)?.message, 'error')
    }
  }

  return !xSmallScreen ? (
    <Button
      disabled={
        !hasAuthority(Authority.EDIT_ACCESS_USINGS) ||
        loading ||
        mutationLoading
      }
      className={classes.button}
      variant="contained"
      onClick={onClick}
      label={text}
      useSmallVersionBreakpoint={false}
    />
  ) : (
    <IconButton
      color="primary"
      onClick={onClick}
      disabled={
        !hasAuthority(Authority.EDIT_ACCESS_USINGS) ||
        loading ||
        mutationLoading
      }
    >
      {type === ToggleType.ACTIVE ? <PowerOff /> : <Power />}
    </IconButton>
  )
}
