import {
  DataProvider,
  UpdateParams,
  UpdateResult,
  GetListResult,
  GetOneParams,
  GetOneResult,
  GetManyParams,
  GetManyResult,
} from 'react-admin'
import { get, getWithPagination, patch } from '../common/fetch.utils'
import { BOOKINGS_URL } from '../api-urls'
import { BookingUpdateRequest } from './booking-update.request'
import { BookingDto } from '../dto/booking.dto'
import filterMapper from './booking-filter.mapper'
import { mapSortBookingParam } from './booking-sort.mapper'
import {
  filterParamsComposer,
  queryParamsComposer,
} from '../common/get-by-conditions.utils'
import { TicketAPIGetListParams } from '../common/ticket-api-get-list.params'

const provider = {
  getList: async (
    resource: string,
    { filter, sort, pagination }: TicketAPIGetListParams,
  ): Promise<GetListResult<BookingDto>> => {
    const filterParams = `a=>${filterParamsComposer('a', filter, filterMapper)}`
    const pathParams = queryParamsComposer(
      sort,
      pagination,
      mapSortBookingParam,
    )
    const path = `/${filterParams}?${pathParams ?? pathParams}`

    const {
      data,
      range: { total },
    } = await getWithPagination<BookingDto[]>(
      `${BOOKINGS_URL}/GetByConditions`,
      path,
    )

    return {
      data,
      total,
    }
  },
  getOne: async (
    resource: string,
    { id: bookingId }: GetOneParams,
  ): Promise<GetOneResult<BookingDto>> => {
    const booking = await get<BookingDto>(BOOKINGS_URL, `/${bookingId}`)

    return {
      data: booking,
    }
  },
  getMany: async (
    resource: string,
    { ids }: GetManyParams,
  ): Promise<GetManyResult<BookingDto>> => {
    const data = await get<BookingDto[]>(
      `${BOOKINGS_URL}/GetByConditions`,
      `/u=>new int[] {${ids.toString()}}.Contains(u.Id)`,
    )
    return Promise.resolve({
      data,
    })
  },
  update: async (
    resource: string,
    { data, previousData }: UpdateParams<BookingUpdateRequest>,
  ): Promise<UpdateResult<BookingDto>> => {
    const booking = await patch<BookingUpdateRequest, BookingDto>(
      BOOKINGS_URL,
      {
        id: data.id,
        disabled:
          data?.disabled !== previousData?.disabled
            ? data?.disabled
            : undefined,
      },
    )
    return Promise.resolve({
      data: { ...booking, ticketsIds: [] },
    })
  },
} as DataProvider

export default provider
