import { createStyles, makeStyles } from '@material-ui/core'
import React, { useState, useReducer, useEffect } from 'react'
import {
  useMutation,
  useNotify,
  useRecordContext,
  useRefresh,
  useShowContext,
} from 'react-admin'
import {
  engineLockButtonInitialState,
  engineLockButtonReducer,
} from '../../../../core/devices/engines/engine-lock-button.reducer'
import { ToggleType } from '../../../../core/devices/engines/engine-lock-button'
import { EngineDto } from '../../../../core/dto/device/engine/engine.dto'
import Button from '../../../common/customized-mui-components/Button'
import { ConfirmDialog, EngineLockDialog } from '../EngineDialogs'
import { useHasAuthority } from '../../../hooks/useHasAuthority'
import { Authority } from '../../../../core/auth/Authority'

const useStyles = makeStyles(() =>
  createStyles({
    button: {
      margin: '2px',
    },
  }),
)

export const EngineLockButton = () => {
  const classes = useStyles()
  const notify = useNotify()
  const hasAuthority = useHasAuthority()
  const { loading } = useShowContext()
  const [{ type, text, mutateQuery }, dispatch] = useReducer(
    engineLockButtonReducer,
    engineLockButtonInitialState,
  )
  const [lockDialogOpen, setLockDialogOpen] = useState(false)
  const [lockName, setLockName] = useState('')
  const [unlockDialogOpen, setUnlockDialogOpen] = useState(false)
  const engine = useRecordContext<EngineDto>()
  const [mutate, { loading: mutationLoading }] = useMutation()
  const refresh = useRefresh()

  useEffect(() => {
    if (engine.disabled) {
      dispatch({ type: ToggleType.UNLOCK })
    } else {
      dispatch({ type: ToggleType.LOCK })
    }
  }, [engine])

  const onClick = async () => {
    if (type === ToggleType.UNLOCK) setUnlockDialogOpen(true)
    else setLockDialogOpen(true)
  }

  const handleLock = async () => {
    try {
      const query = mutateQuery?.(engine, lockName)
      await mutate(query, { returnPromise: true })
      refresh()
    } catch (err) {
      notify((err as Error)?.message, 'error')
    }
  }

  return (
    <>
      <ConfirmDialog
        open={unlockDialogOpen}
        confirm={handleLock}
        source="lock"
        cancel={() => setUnlockDialogOpen(false)}
        action="remove"
      />
      <EngineLockDialog
        open={lockDialogOpen}
        close={() => setLockDialogOpen(false)}
        lockName={lockName}
        setLockName={setLockName}
        handleLock={handleLock}
      />
      <Button
        disabled={
          !hasAuthority(Authority.EDIT_ENGINES) || loading || mutationLoading
        }
        className={classes.button}
        variant="contained"
        onClick={onClick}
        label={text}
        useSmallVersionBreakpoint={false}
      />
    </>
  )
}
