import { Record as RaRecord } from 'ra-core'
import {
  STATIC_ENTRIES_KEY,
  TRANSITIONAL_STATIC_ENTRIES_KEY,
} from './csv.consts'

export interface FormMappings {
  [key: string]: string | Record<string, string> | undefined
  [STATIC_ENTRIES_KEY]?: Record<string, string>
}

export interface TransitionalFormMappings extends FormMappings {
  [TRANSITIONAL_STATIC_ENTRIES_KEY]?: Record<string, string>
}

export interface CreateManyParams<T = any> {
  data: T[]
}

export interface CreateManyResult<T = RaRecord> {
  data: T[]
}

export type SelectField = [name: string, required?: boolean]

export type CustomMapper = (formValues: FormMappings) => FormMappings
export type FormValues = Record<string, string>

export const IMPORT_PROVIDER_METHOD_TYPE = 'createMany'
