import React from 'react'
import { makeStyles, useMediaQuery, useTheme } from '@material-ui/core'
import {
  CreateProps,
  ReferenceInput,
  required,
  useTranslate,
} from 'react-admin'
import { SelectField, STATIC_ENTRIES_KEY } from '../../core/csv'
import { Import } from '../common/import/Import'
import { ResourceName } from '../../core/ResourceName'
import { SelectWithPaginationInput } from '../common/SelectWithPaginationInput'
import { BlockType } from '../../core/enum/BlockType'

const useStyles = makeStyles((theme) => ({
  input: {
    width: theme.spacing(40),
  },
}))

export const TicketImport = (props: CreateProps) => {
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const t = useTranslate()
  const classes = useStyles()

  const selectFields: SelectField[] = [
    ['customerFirstName', true],
    ['customerLastname', true],
    ['customerPesel', true],
    ['customerDocumentNumber', true],
    ['ticketCode', true],
    ['ticketType', true],
    ['bookingType'],
    ['disabled'],
  ]

  return (
    <Import selectFields={selectFields} {...props}>
      <ReferenceInput
        className={classes.input}
        label={t('common.import.mapping-input-label', { fieldName: 'blockId' })}
        source={`${STATIC_ENTRIES_KEY}.blockId`}
        reference={ResourceName.BLOCKS}
        sort={{ field: 'name', order: 'ASC' }}
        filter={{ type: BlockType.DYNAMIC }}
        perPage={smallScreen ? 5 : 15}
        validate={required()}
      >
        <SelectWithPaginationInput
          optionText="name"
          paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
        />
      </ReferenceInput>
      <ReferenceInput
        className={classes.input}
        label={t('common.import.mapping-input-label', {
          fieldName: 'variantId',
        })}
        source={`${STATIC_ENTRIES_KEY}.variantId`}
        reference={ResourceName.VARIANTS}
        sort={{ field: 'name', order: 'ASC' }}
        perPage={smallScreen ? 5 : 15}
        validate={required()}
      >
        <SelectWithPaginationInput
          optionText="name"
          paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
        />
      </ReferenceInput>
      <ReferenceInput
        className={classes.input}
        label={t('common.import.mapping-input-label', { fieldName: 'eventId' })}
        source={`${STATIC_ENTRIES_KEY}.eventId`}
        reference={ResourceName.EVENTS}
        sort={{ field: 'name', order: 'ASC' }}
        perPage={smallScreen ? 5 : 15}
        validate={required()}
      >
        <SelectWithPaginationInput
          optionText="name"
          paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
        />
      </ReferenceInput>
    </Import>
  )
}
