import React from 'react'
import {
  Datagrid,
  List,
  ListProps,
  Pagination,
  TextField,
  TextInput,
  useListContext,
  Filter,
} from 'react-admin'
import { ResourceName } from '../../../core/ResourceName'
import FilterProps from '../../common/FilterProps'

const RolesFilters = (props: FilterProps) => (
  <Filter {...props}>
    <TextInput source="name" alwaysOn />
  </Filter>
)

const RolesGrid = () => {
  const { loading } = useListContext()
  return (
    <Datagrid
      size="small"
      loaded={!loading}
      rowClick={(id) => `${ResourceName.ROLES}/${id}/show`}
    >
      <TextField source="id" label="ID" />
      <TextField source="name" />
    </Datagrid>
  )
}

export const RoleList = ({ ...props }: ListProps) => (
  <List
    {...props}
    perPage={20}
    pagination={<Pagination rowsPerPageOptions={[10, 20, 50]} />}
    exporter={false}
    filters={<RolesFilters />}
  >
    <RolesGrid />
  </List>
)
