import { useMediaQuery, useTheme } from '@material-ui/core'
import { Cancel } from '@material-ui/icons'
import React from 'react'
import {
  Create,
  CreateProps,
  useTranslate,
  SimpleForm,
  TextInput,
  SaveButton,
  ListButton,
  Toolbar,
  ToolbarProps,
  SelectInput,
  ReferenceInput,
} from 'react-admin'
import { Authority } from '../../../../core/auth/Authority'
import { CameraDto } from '../../../../core/dto/device/camera/cameras/camera.dto'
import { CameraTypes } from '../../../../core/enum/CameraTypes'
import { ResourceName } from '../../../../core/ResourceName'
import { SelectWithPaginationInput } from '../../../common/SelectWithPaginationInput'
import { useHasAuthority } from '../../../hooks/useHasAuthority'

export const validateCameraCreate = (values: CameraDto) => {
  const errors: { [n: string]: string } = {}
  if (!values.description) {
    errors.desciption = 'ra.validation.required'
  }
  if (!values.serverSideName) {
    errors.serverSideName = 'ra.validation.required'
  }
  if (!values.type) {
    errors.type = 'ra.validation.required'
  }
  if (!values.serverId) {
    errors.serverId = 'ra.validation.required'
  }
  return errors
}

const CreateTitle = () => {
  const translate = useTranslate()
  return <span>{translate('resources.cameras.titles.create')}</span>
}

const CreateToolbar = (props: ToolbarProps) => (
  <Toolbar {...props}>
    <SaveButton />
    <ListButton icon={<Cancel />} label="const.cancel" />
  </Toolbar>
)

const Camera = ({ ...props }) => {
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const hasAuthority = useHasAuthority()

  return (
    <SimpleForm
      {...props}
      redirect="show"
      toolbar={<CreateToolbar />}
      validate={validateCameraCreate}
    >
      <TextInput source="description" isRequired />
      <TextInput source="serverSideName" isRequired />
      <SelectInput
        source="type"
        defaultValue={CameraTypes.RTSP}
        choices={Object.entries(CameraTypes).map(([value]) => ({
          id: value,
          name: value,
        }))}
        isRequired
      />
      {hasAuthority(Authority.VIEW_CAMERA_SERVERS) && (
        <ReferenceInput
          source="serverId"
          reference={ResourceName.CAMERA_SERVERS}
          sort={{ field: 'name', order: 'ASC' }}
          perPage={smallScreen ? 5 : 15}
          isRequired
        >
          <SelectWithPaginationInput
            optionText="name"
            paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
          />
        </ReferenceInput>
      )}
    </SimpleForm>
  )
}

export const CameraCreate = (props: CreateProps) => (
  <Create {...props} hasShow={false} title={<CreateTitle />}>
    <Camera />
  </Create>
)
