import React, { ReactElement } from 'react'
import {
  useTranslate,
  MenuItemLink,
  ReduxState,
  ResourceDefinition,
  getResources,
} from 'react-admin'
import { useSelector } from 'react-redux'
import { ResourceName } from '../../core/ResourceName'

interface MenuItemProps {
  resource: ResourceName
  icon: ReactElement
}

const MenuItem = (props: MenuItemProps) => {
  const { resource, icon } = props
  const translate = useTranslate()

  const availableResources = new Set(
    useSelector<ReduxState, ResourceDefinition[]>(getResources).map(
      (r) => r.name as ResourceName,
    ),
  )

  return (
    <>
      {availableResources.has(resource) && (
        <MenuItemLink
          to={`/${resource}`}
          primaryText={translate(`resources.${resource}.name`)}
          leftIcon={icon}
        />
      )}
    </>
  )
}

export default MenuItem
