import { DataProvider, GetListResult } from 'react-admin'
import { EVENTS_URL } from '../../../api-urls'
import { getWithPagination } from '../../../common/fetch.utils'
import {
  filterParamsComposer,
  queryParamsComposer,
} from '../../../common/get-by-conditions.utils'
import { NameFilter } from '../../../common/name.filter'
import { EventDto } from '../../../dto/event/event.dto'
import { EventOverallView } from './event-overall-view'
import filterMapper from './overall-view-list-filter.mapper'
import { mapSortOverallViewParam } from './overall-view-list-sort.mapper'
import { mapToEventOverallView } from './overall-view-list.mapper'

const provider = {
  getList: async (
    resource: string,
    { filter, sort, pagination }: NameFilter,
  ): Promise<GetListResult<EventOverallView>> => {
    const filterParams = `o=>${filterParamsComposer('o', filter, filterMapper)}`
    const pathParams = queryParamsComposer(
      sort,
      pagination,
      mapSortOverallViewParam,
    )
    const path = `/${filterParams}?${pathParams ?? pathParams}`

    const {
      data,
      range: { total },
    } = await getWithPagination<EventDto[]>(
      `${EVENTS_URL}/GetByConditions`,
      path,
    )

    const overallViewList = data.map((event) => mapToEventOverallView(event))

    return {
      data: overallViewList,
      total,
    }
  },
} as DataProvider

export default provider
