import {
  createStyles,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  makeStyles,
  TextField,
  Theme,
} from '@material-ui/core'
import React, { useState } from 'react'
import { useTranslate } from 'react-admin'
import Button from '../../common/customized-mui-components/Button'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    dialogProperty: {
      marginBottom: theme.spacing(1),
    },
  }),
)

export const ConfirmDialog = ({
  confirm,
  cancel,
  source,
  open,
  action,
}: {
  confirm: () => void
  cancel: () => void
  source: 'deviceId' | 'lock'
  open: boolean
  action: 'set' | 'remove'
}) => {
  const translate = useTranslate()
  return (
    <Dialog open={open}>
      <DialogTitle>
        {translate(`resources.palms.dialogs.confirm.${source}.${action}`)}
      </DialogTitle>
      <DialogActions>
        <Button
          variant="contained"
          onClick={confirm}
          label={translate('ra.action.save')}
          useSmallVersionBreakpoint={false}
        />
        <Button
          variant="contained"
          onClick={cancel}
          label={translate('const.cancel')}
          useSmallVersionBreakpoint={false}
        />
      </DialogActions>
    </Dialog>
  )
}

export const PalmDeviceIdDialog = ({
  open,
  close,
  deviceId,
  setDeviceId,
  handleLock,
}: {
  open: boolean
  close: () => void
  deviceId: string
  setDeviceId: (value: string) => void
  handleLock: () => void
}) => {
  const classes = useStyles()
  const [error, setError] = useState(false)
  const [errorText, setErrorText] = useState('')
  const [confirmOpen, setConfirmOpen] = useState(false)
  const translate = useTranslate()

  const handleClose = () => {
    setDeviceId('')
    setError(false)
    setErrorText('')
    close()
  }

  return (
    <Dialog open={open} onClose={handleClose}>
      <ConfirmDialog
        open={confirmOpen}
        cancel={() => setConfirmOpen(false)}
        confirm={handleLock}
        source="deviceId"
        action="set"
      />
      <DialogTitle>
        {translate('resources.palms.dialogs.deviceId.title')}
      </DialogTitle>
      <DialogContent>
        <TextField
          className={classes.dialogProperty}
          variant="outlined"
          value={deviceId}
          onChange={(e) => setDeviceId(e.target.value)}
          fullWidth
          error={error}
          helperText={errorText}
        />
      </DialogContent>
      <DialogActions>
        <Button
          variant="contained"
          onClick={() => setConfirmOpen(true)}
          label={translate('ra.action.save')}
          useSmallVersionBreakpoint={false}
        />
        <Button
          variant="contained"
          onClick={handleClose}
          label={translate('const.cancel')}
          useSmallVersionBreakpoint={false}
        />
      </DialogActions>
    </Dialog>
  )
}

