import {
  Box,
  Grid,
  makeStyles,
  Theme,
  useMediaQuery,
  useTheme,
} from '@material-ui/core'
import * as React from 'react'
import {
  Datagrid,
  DateField,
  Filter,
  List,
  Pagination,
  ReferenceField,
  ResourceComponentProps,
  TextField,
  TextInput,
  useListContext,
  useTranslate,
  ReferenceInput,
  SelectInput,
  FunctionField,
} from 'react-admin'
import { Authority } from '../../core/auth/Authority'
import { TicketDto } from '../../core/dto/ticket.dto'
import { BookingType } from '../../core/enum/BookingType'
import { ResourceName } from '../../core/ResourceName'
import FilterProps from '../common/FilterProps'
import { MaskField } from '../common/MaskField'
import { SelectWithPaginationInput } from '../common/SelectWithPaginationInput'
import { useHasAuthority } from '../hooks/useHasAuthority'

const useStyles = makeStyles((theme: Theme) => ({
  inactiveTicketCode: {
    color: theme.palette.error.main,
    textDecoration: 'line-through',
    textDecorationColor: theme.palette.error.main,
  },
}))

const TicketExpand = () => {
  const t = useTranslate()
  return (
    <Box padding={1}>
      <Grid container spacing={1}>
        <Grid item xs={4}>
          {t('resources.tickets.fields.bookingId')}
        </Grid>
        <Grid item xs={8} style={{ textAlign: 'center' }}>
          <TextField source="bookingId" />
        </Grid>
      </Grid>
    </Box>
  )
}

const TicketGrid = ({ ...props }) => {
  const classes = useStyles()
  const { data } = useListContext()
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const xSmallScreen = useMediaQuery(theme.breakpoints.down('xs'))
  const hasAuthority = useHasAuthority()

  return (
    <>
      <Datagrid
        {...props}
        padding={smallScreen ? 'checkbox' : 'default'}
        rowClick={(id) =>
          `${ResourceName.BOOKINGS}/${data[id]?.bookingId}/show`
        }
        isRowExpandable={() => xSmallScreen}
        expand={<TicketExpand />}
        hasBulkActions={false}
      >
        {!smallScreen &&
          hasAuthority(Authority.VIEW_BOOKINGS) &&
          hasAuthority(Authority.VIEW_CUSTOMERS) && (
            <ReferenceField
              source="bookingId"
              label="resources.tickets.fields.firstname"
              reference={ResourceName.BOOKINGS}
              link={false}
              sortBy="customerFirstname"
            >
              <ReferenceField
                source="customerId"
                reference={ResourceName.CUSTOMERS}
                link={false}
              >
                <TextField source="firstname" />
              </ReferenceField>
            </ReferenceField>
          )}
        {!smallScreen &&
          hasAuthority(Authority.VIEW_BOOKINGS) &&
          hasAuthority(Authority.VIEW_CUSTOMERS) && (
            <ReferenceField
              source="bookingId"
              label="resources.tickets.fields.lastname"
              reference={ResourceName.BOOKINGS}
              link={false}
              sortBy="customerLastname"
            >
              <ReferenceField
                source="customerId"
                reference={ResourceName.CUSTOMERS}
                link={false}
              >
                <TextField source="lastname" />
              </ReferenceField>
            </ReferenceField>
          )}
        {!smallScreen &&
          hasAuthority(Authority.VIEW_BOOKINGS) &&
          hasAuthority(Authority.VIEW_CUSTOMERS) && (
            <ReferenceField
              source="bookingId"
              label="resources.tickets.fields.pesel"
              reference={ResourceName.BOOKINGS}
              link={false}
              sortBy="customerPesel"
            >
              <ReferenceField
                source="customerId"
                reference={ResourceName.CUSTOMERS}
                link={false}
              >
                <MaskField source="pesel" />
              </ReferenceField>
            </ReferenceField>
          )}
        {smallScreen &&
          hasAuthority(Authority.VIEW_BOOKINGS) &&
          hasAuthority(Authority.VIEW_CUSTOMERS) && (
            <ReferenceField
              source="bookingId"
              label="resources.tickets.fields.customer"
              reference={ResourceName.BOOKINGS}
              link={false}
              sortBy="customerPesel"
            >
              <ReferenceField
                source="customerId"
                reference={ResourceName.CUSTOMERS}
                link={false}
              >
                <>
                  <TextField source="firstname" />{' '}
                  <TextField source="lastname" /> <MaskField source="pesel" />
                </>
              </ReferenceField>
            </ReferenceField>
          )}
        <FunctionField<TicketDto>
          source="ticketCode"
          render={(record?: TicketDto) =>
            record?.active === true ? (
              <TextField source="ticketCode" />
            ) : (
              <TextField
                source="ticketCode"
                className={classes.inactiveTicketCode}
              />
            )
          }
        />
        {hasAuthority(Authority.VIEW_BOOKINGS) &&
          hasAuthority(Authority.VIEW_EVENTS) && (
            <ReferenceField
              source="bookingId"
              label="resources.tickets.fields.event"
              reference={ResourceName.BOOKINGS}
              link={false}
              sortBy="eventName"
            >
              <ReferenceField
                source="eventId"
                reference={ResourceName.EVENTS}
                link={false}
              >
                <>
                  <TextField source="name" /> <DateField source="dateStart" /> (
                  <TextField source="id" />)
                </>
              </ReferenceField>
            </ReferenceField>
          )}
        {!xSmallScreen && <TextField source="bookingId" />}
      </Datagrid>
    </>
  )
}

const TicketFilters = (props: FilterProps) => {
  const hasAuthority = useHasAuthority()
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))

  return (
    <Filter {...props}>
      <TextInput source="ticketCode" alwaysOn />
      {hasAuthority(Authority.VIEW_EVENTS) && (
        <ReferenceInput
          label="resources.tickets.fields.event"
          source="eventId"
          reference={ResourceName.EVENTS}
          sort={{ field: 'dateStart', order: 'DESC' }}
          perPage={smallScreen ? 5 : 15}
        >
          <SelectWithPaginationInput
            optionText="name"
            paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
          />
        </ReferenceInput>
      )}
      <SelectInput
        source="bookingType"
        choices={Object.entries(BookingType).map(([value]) => ({
          id: value,
          name: `resources.enums.bookingType.${value}`,
        }))}
      />
      {hasAuthority(Authority.VIEW_OBJECTS) && (
        <ReferenceInput
          source="objectId"
          reference={ResourceName.OBJECTS}
          sort={{ field: 'name', order: 'ASC' }}
          perPage={smallScreen ? 5 : 15}
        >
          <SelectWithPaginationInput
            optionText="name"
            paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
          />
        </ReferenceInput>
      )}
      {hasAuthority(Authority.VIEW_VARIANTS) && (
        <ReferenceInput
          source="variantId"
          reference={ResourceName.VARIANTS}
          sort={{ field: 'name', order: 'ASC' }}
          perPage={smallScreen ? 5 : 15}
        >
          <SelectWithPaginationInput
            optionText="name"
            paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
          />
        </ReferenceInput>
      )}
    </Filter>
  )
}

export const TicketList = (props: ResourceComponentProps) => (
  <List
    {...props}
    exporter={false}
    perPage={20}
    sort={{ field: 'id', order: 'DESC' }}
    pagination={<Pagination rowsPerPageOptions={[10, 20, 50]} />}
    filters={<TicketFilters />}
  >
    <TicketGrid />
  </List>
)
