import { Typography } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import { Settings } from '@material-ui/icons'
import React from 'react'
import {
  AppBar,
  AppBarProps,
  CoreLayoutProps,
  UserMenu,
  UserMenuProps,
  MenuItemLink,
  useTranslate,
} from 'react-admin'
import { useSelector } from 'react-redux'
import { DarkTheme, LightTheme } from './app.theme'
import { ThemeState } from '../../core/common/theme.reducer'
import { AppMenu } from './AppMenu'
import { Layout } from '../../lib/@react-admin/ra-enterprise/esm/src'

const useStyles = makeStyles({
  title: {
    flex: 1,
    textOverflow: 'ellipsis',
    whiteSpace: 'nowrap',
    overflow: 'hidden',
  },
})

const AppUserMenu = (props: UserMenuProps) => {
  const translate = useTranslate()
  return (
    <UserMenu {...props}>
      <MenuItemLink
        to="/configuration"
        primaryText={translate('settings.name')}
        leftIcon={<Settings />}
      />
    </UserMenu>
  )
}

const AppTopBar = (props: AppBarProps) => {
  const classes = useStyles()
  return (
    <AppBar {...props} userMenu={<AppUserMenu />}>
      <Typography
        variant="h6"
        className={classes.title}
        id="react-admin-title"
      />
    </AppBar>
  )
}

export const AppLayout = (props: CoreLayoutProps) => {
  const theme = useSelector((state: ThemeState) =>
    state.theme.darkMode === 'dark' ? DarkTheme : LightTheme,
  )

  return <Layout {...props} menu={AppMenu} appBar={AppTopBar} theme={theme} />
}
