import { sanitizeFetchType } from 'react-admin'
import { PassageDto } from '../../dto/device/passages/passage.dto'
import { PassageDirections } from '../../enum/PassageDirections'
import { ResourceName } from '../../ResourceName'
import {
  ToggleButtonAction,
  ToggleButtonState,
  ToggleType,
} from './passage-set-boolean-status-button'

type Mutation = {
  type: string
  resource: string
  // eslint-disable-next-line @typescript-eslint/ban-types
  payload?: object
}

const turnOffMutation = ({ id }: PassageDto): Mutation => ({
  type: sanitizeFetchType('setFreePassState'),
  resource: ResourceName.PASSAGES,
  payload: {
    passageId: id,
    freePassState: false,
    direction: PassageDirections.IN,
  },
})

const turnOnMutation = ({ id }: PassageDto): Mutation => ({
  type: sanitizeFetchType('setFreePassState'),
  resource: ResourceName.PASSAGES,
  payload: {
    passageId: id,
    freePassState: true,
    direction: PassageDirections.IN,
  },
})

type State = {
  mutateQuery?: (passage: PassageDto) => Mutation
} & ToggleButtonState

export const passageSetFreePassForwardButtonInitialState = {
  type: ToggleType.OFF,
  text: 'resources.passages.actions.setFreePassState.setForward',
}

export const passageSetFreePassForwardButtonReducer = (
  state: State,
  { type }: ToggleButtonAction,
): State => {
  switch (type) {
    case ToggleType.OFF: {
      return {
        type,
        text: 'resources.passages.actions.setFreePassState.setForward',
        mutateQuery: turnOnMutation,
      }
    }
    case ToggleType.ON: {
      return {
        type,
        text: 'resources.passages.actions.setFreePassState.resetForward',
        mutateQuery: turnOffMutation,
      }
    }
    default:
      throw new Error()
  }
}
