import { useMediaQuery, useTheme } from '@material-ui/core'
import { Cancel } from '@material-ui/icons'
import React, { useState } from 'react'
import {
  BooleanInput,
  Create,
  CreateProps,
  ListButton,
  ReferenceInput,
  SaveButton,
  SelectInput,
  SimpleForm,
  TextInput,
  Toolbar,
  ToolbarProps,
  useTranslate,
} from 'react-admin'
import { Authority } from '../../../core/auth/Authority'
import { PassageDto } from '../../../core/dto/device/passages/passage.dto'
import { HardwareVersions } from '../../../core/enum/HardwareVersions'
import { ResourceName } from '../../../core/ResourceName'
import { SelectWithPaginationInput } from '../../common/SelectWithPaginationInput'
import { useHasAuthority } from '../../hooks/useHasAuthority'
import { SPOM4FullAddressInput } from './SPOM4Address'

const validatePassageCreate = (values: PassageDto) => {
  const errors: { [n: string]: string } = {}
  if (!values.name) {
    errors.name = 'ra.validation.required'
  }
  if (values.hardwareVersion === HardwareVersions.SPOM4) {
    const addressParts = values.address?.split('&')
    if (!addressParts?.[0]) {
      errors.spom4_ipGate = 'ra.validation.required'
    }
    if (!addressParts?.[1]) {
      errors.spom4_tscanHost = 'ra.validation.required'
    }
    if (!addressParts?.[2]) {
      errors.spom4_direction = 'ra.validation.required'
    }
    if (!addressParts?.[3]) {
      errors.spom4_rs485Address = 'ra.validation.required'
    }
  } else if (!values.address) {
    errors.address = 'ra.validation.required'
  }
  if (Number.isNaN(Number(values.freeEntrancesCount)))
    errors.freeEntrancesCount = 'ra.validation.not_numeric'
  if (Number.isNaN(Number(values.freeExitsCount)))
    errors.freeExitsCount = 'ra.validation.not_numeric'
  if (Number.isNaN(Number(values.ticketEntrancesCount)))
    errors.ticketEntrancesCount = 'ra.validation.not_numeric'
  if (Number.isNaN(Number(values.ticketExitsCount)))
    errors.ticketExitsCount = 'ra.validation.not_numeric'
  if (Number.isNaN(Number(values.unauthorizedEntrancesCount)))
    errors.unauthorizedEntrancesCount = 'ra.validation.not_numeric'
  if (Number.isNaN(Number(values.unauthorizedExitsCount)))
    errors.unauthorizedExitsCount = 'ra.validation.not_numeric'
  return errors
}

const CreateTitle = () => {
  const translate = useTranslate()
  return <span>{translate('resources.passages.titles.create')}</span>
}

const CreateToolbar = (props: ToolbarProps) => (
  <Toolbar {...props}>
    <SaveButton />
    <ListButton icon={<Cancel />} label="const.cancel" />
  </Toolbar>
)

export const PassageCreate = (props: CreateProps) => {
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const hasAuthority = useHasAuthority()
  const { record: passage } = props
  const [hardwareVersion, setHardwareVersion] = useState(
    passage?.hardwareVersion
      ? passage?.hardwareVersion
      : HardwareVersions.MODULO5,
  )

  const defaultValues = {
    hardwareVersion: HardwareVersions.MODULO5,
  }

  return (
    <Create {...props} title={<CreateTitle />}>
      <SimpleForm
        redirect="show"
        validate={validatePassageCreate}
        toolbar={<CreateToolbar />}
        initialValues={defaultValues}
      >
        <TextInput source="name" isRequired />
        <BooleanInput source="isWatched" />
        {hardwareVersion === HardwareVersions.SPOM4 ? (
          <SPOM4FullAddressInput source="address" />
        ) : (
          <TextInput source="address" isRequired />
        )}
        {hasAuthority(Authority.VIEW_DEVICE_CATEGORIES) && (
          <ReferenceInput
            source="categoryId"
            reference={ResourceName.DEVICE_CATEGORIES}
            sort={{ field: 'name', order: 'ASC' }}
            perPage={smallScreen ? 5 : 15}
          >
            <SelectWithPaginationInput
              optionText="name"
              paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
            />
          </ReferenceInput>
        )}
        <TextInput source="freeEntrancesCount" defaultValue={0} />
        <TextInput source="freeExitsCount" defaultValue={0} />
        <TextInput source="ticketEntrancesCount" defaultValue={0} />
        <TextInput source="ticketExitsCount" defaultValue={0} />
        <TextInput source="unauthorizedEntrancesCount" defaultValue={0} />
        <TextInput source="unauthorizedExitsCount" defaultValue={0} />
        <SelectInput
          value={hardwareVersion}
          onChange={(e) => setHardwareVersion(e.target.value)}
          source="hardwareVersion"
          choices={Object.entries(HardwareVersions).map(([value]) => ({
            id: value,
            name: `resources.enums.hardwareVersions.${value}`,
          }))}
        />
      </SimpleForm>
    </Create>
  )
}
