import { useMediaQuery, useTheme } from '@material-ui/core'
import React from 'react'
import {
  Datagrid,
  Filter,
  List,
  ListProps,
  Pagination,
  ReferenceField,
  ReferenceInput,
  TextField,
  TextInput,
} from 'react-admin'
import { Authority } from '../../../../core/auth/Authority'
import { ResourceName } from '../../../../core/ResourceName'
import FilterProps from '../../../common/FilterProps'
import { SelectWithPaginationInput } from '../../../common/SelectWithPaginationInput'
import { useHasAuthority } from '../../../hooks/useHasAuthority'

const CameraFilters = (props: FilterProps) => {
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const hasAuthority = useHasAuthority()

  return (
    <Filter {...props}>
      <TextInput source="description" alwaysOn />
      {hasAuthority(Authority.VIEW_CAMERA_SERVERS) && (
        <ReferenceInput
          label="resources.cameras.fields.serverId"
          source="serverId"
          reference={ResourceName.CAMERA_SERVERS}
          sort={{ field: 'name', order: 'ASC' }}
          perPage={smallScreen ? 5 : 15}
        >
          <SelectWithPaginationInput
            optionText="name"
            paginationProps={{ rowsPerPageOptions: [smallScreen ? 5 : 15] }}
          />
        </ReferenceInput>
      )}
    </Filter>
  )
}

const CameraGrid = ({ ...props }) => {
  const hasAuthority = useHasAuthority()
  const theme = useTheme()
  const xSmallScreen = useMediaQuery(theme.breakpoints.down('xs'))

  return (
    <Datagrid
      {...props}
      size="small"
      rowClick={(id) => `/${ResourceName.CAMERAS}/${id}/show`}
      hasBulkActions={false}
    >
      <TextField source="id" label="ID" />
      <TextField source="description" />
      {!xSmallScreen && <TextField source="serverSideName" />}
      {hasAuthority(Authority.VIEW_CAMERA_SERVERS) && (
        <ReferenceField
          source="serverId"
          reference={ResourceName.CAMERA_SERVERS}
          link={false}
        >
          <TextField source="name" />
        </ReferenceField>
      )}
    </Datagrid>
  )
}

export const CameraList = ({ ...props }: ListProps) => (
  <List
    {...props}
    perPage={20}
    pagination={<Pagination rowsPerPageOptions={[10, 20, 50]} />}
    exporter={false}
    sort={{
      field: 'id',
      order: 'DESC',
    }}
    filters={<CameraFilters />}
  >
    <CameraGrid />
  </List>
)
