import { createStyles, makeStyles, Theme } from '@material-ui/core'
import * as React from 'react'
import { useEffect, useReducer } from 'react'
import {
  GET_MANY,
  GET_ONE,
  sanitizeFetchType,
  useMutation,
  useNotify,
  useQueryWithStore,
  useRecordContext,
  useRefresh,
  useShowContext,
} from 'react-admin'
import { Authority } from '../../../core/auth/Authority'
import {
  blockAccessButtonInitialState,
  toggleAccessButtonReducer,
} from '../../../core/bookings/toggle-booking-access-button.reducer'
import { ToggleType } from '../../../core/bookings/toggle-booking-blockade-button'
import { AccessUsingDto } from '../../../core/dto/access-using.dto'
import { BookingDto } from '../../../core/dto/booking.dto'
import { TicketDto } from '../../../core/dto/ticket.dto'
import { ResourceName } from '../../../core/ResourceName'
import Button from '../../common/customized-mui-components/Button'
import { useHasAuthority } from '../../hooks/useHasAuthority'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    button: {
      color: theme.palette.common.white,
      marginLeft: theme.spacing(1),
    },
  }),
)

export const ToggleBookingAccessButton = () => {
  const classes = useStyles()
  const notify = useNotify()
  const hasAuthority = useHasAuthority()
  const { loading } = useShowContext()
  const [{ type, text, mutateQuery }, dispatch] = useReducer(
    toggleAccessButtonReducer,
    blockAccessButtonInitialState,
  )
  const ticketDetails = useRecordContext<BookingDto>()
  const [mutate, { loading: mutationLoading }] = useMutation()
  const refresh = useRefresh()

  const { data: tickets } = useQueryWithStore({
    type: sanitizeFetchType(GET_MANY),
    resource: ResourceName.TICKETS,
    payload: {
      ids: ticketDetails?.ticketsIds,
    },
  })

  const { data: accessUsings } = useQueryWithStore({
    type: sanitizeFetchType(GET_MANY),
    resource: ResourceName.ACCESS_USINGS,
    payload: {
      ids: tickets
        ? tickets.flatMap((ticket: TicketDto) => ticket.accessUsingsIds)
        : [],
    },
  })

  const isActive = (au?: AccessUsingDto[]) => au && au.some((a) => a.active)

  useEffect(() => {
    if (isActive(accessUsings)) {
      dispatch({ type: ToggleType.UNBLOCK })
    } else {
      dispatch({ type: ToggleType.BLOCK })
    }
  }, [accessUsings])

  const onClick = async () => {
    try {
      const { data: variant } =
        type === ToggleType.BLOCK
          ? await mutate(
              {
                type: sanitizeFetchType(GET_ONE),
                resource: ResourceName.VARIANTS,
                payload: {
                  id: ticketDetails?.variantId,
                },
              },
              { returnPromise: true },
            )
          : { data: null }

      const query = mutateQuery?.(ticketDetails, variant, accessUsings)
      await mutate(query, { returnPromise: true })
      refresh()
    } catch (err) {
      notify((err as Error)?.message, 'error')
    }
  }

  return (
    <Button
      disabled={
        !hasAuthority(Authority.EDIT_BOOKINGS) ||
        type === ToggleType.DISABLED ||
        loading ||
        mutationLoading
      }
      className={classes.button}
      variant="contained"
      onClick={onClick}
      label={text}
      useSmallVersionBreakpoint={false}
    />
  )
}
