import { Box } from '@material-ui/core'
import { useListContext } from 'ra-core'
import React from 'react'
import {
  FilterButton,
  List,
  ListActionsProps,
  ListProps,
  Pagination,
  TextField,
  TextInput,
  TopToolbar,
  useTranslate,
} from 'react-admin'
import {
  EditableDatagrid,
  RowForm,
} from '../../../lib/@react-admin/ra-editable-datagrid'
import ParameterizedCreateButton from '../../common/ParameterizedCreateButton'
import Empty from '../Empty'

const ListFilters = () => [
  <TextInput source="numberAlias" alwaysOn />,
  <TextInput source="id" />,
  <TextInput source="rowAlias" />,
  <TextInput source="blockId" />,
]

const EditForm = ({ ...props }) => (
  <RowForm {...props}>
    <TextField source="id" />
    <TextInput source="rowAlias" />
    <TextInput source="numberAlias" />
    <TextField source="blockId" />
  </RowForm>
)

const SeatsGrid = ({ ...props }) => (
  <EditableDatagrid {...props} size="small" editForm={<EditForm />} noDelete>
    <TextField source="id" />
    <TextField source="rowAlias" />
    <TextField source="numberAlias" />
    <TextField source="blockId" />
  </EditableDatagrid>
)

interface SeatListActions extends ListActionsProps {
  objectId?: number
}

const ListActions = ({ objectId }: SeatListActions) => (
  <TopToolbar>
    <FilterButton />
    <ParameterizedCreateButton searchParams={{ objectId }} />
  </TopToolbar>
)

const EmptyView = ({ objectId }: { objectId?: number }) => {
  const translate = useTranslate()
  return (
    <Box>
      <Empty text={translate('resources.seats.empty')} />
      <TopToolbar>
        <ParameterizedCreateButton searchParams={{ objectId }} />
      </TopToolbar>
    </Box>
  )
}

interface SeatsListProps extends ListProps {
  objectId?: number
}

const SeatsList = ({ objectId, ...props }: SeatsListProps) => {
  const { basePath } = useListContext()

  return (
    <List
      {...props}
      exporter={false}
      basePath={basePath}
      filters={ListFilters()}
      actions={<ListActions objectId={objectId} />}
      bulkActionButtons={false}
      perPage={20}
      pagination={<Pagination rowsPerPageOptions={[10, 20, 50]} />}
      empty={<EmptyView objectId={objectId} />}
    >
      <SeatsGrid />
    </List>
  )
}

export default SeatsList
