export const getContrastColor = (hexcolor: string) => {
  const convertedColor = hexcolor.replace('#', '')
  const r = parseInt(convertedColor.substring(1, 2), 16)
  const g = parseInt(convertedColor.substring(3, 2), 16)
  const b = parseInt(convertedColor.substring(5, 2), 16)
  const yiq = (r * 299 + g * 587 + b * 114) / 1000
  return yiq >= 128 ? 'black' : 'white'
}
