import { createStyles, makeStyles } from '@material-ui/core'
import React, { useState, useReducer, useEffect } from 'react'
import {
  useMutation,
  useNotify,
  useRecordContext,
  useRefresh,
  useShowContext,
} from 'react-admin'
import Button from '../../../common/customized-mui-components/Button'
import { ToggleType } from '../../../../core/devices/passages/passage-set-boolean-status-button'
import { PassageDto } from '../../../../core/dto/device/passages/passage.dto'
import { ConfirmDialog } from '../PassageDialogs'
import { useHasAuthority } from '../../../hooks/useHasAuthority'
import { Authority } from '../../../../core/auth/Authority'
import {
  passageSetFreePassForwardButtonInitialState,
  passageSetFreePassForwardButtonReducer,
} from '../../../../core/devices/passages/passage-set-free-pass-forward-button.reducer'

const useStyles = makeStyles(() =>
  createStyles({
    button: {
      margin: '2px',
    },
  }),
)

export const PassageSetFreePassForwardButton = ({ ...props }) => {
  const { jsonStatus: rawJsonStatus, disabled } = props
  const classes = useStyles()
  const notify = useNotify()
  const hasAuthority = useHasAuthority()
  const { loading } = useShowContext()
  const [{ type, text, mutateQuery }, dispatch] = useReducer(
    passageSetFreePassForwardButtonReducer,
    passageSetFreePassForwardButtonInitialState,
  )
  const [confirmDialogOpen, setConfirmDialogOpen] = useState(false)
  const passage = useRecordContext<PassageDto>()
  const [mutate, { loading: mutationLoading }] = useMutation()
  const refresh = useRefresh()

  useEffect(() => {
    let jsonStatus
    try {
      jsonStatus = JSON.parse(rawJsonStatus)
      if (jsonStatus === null || jsonStatus === undefined) jsonStatus = []
    } catch {
      jsonStatus = []
    }
    if (jsonStatus.some((status: any) => status?.FreePassForward)) {
      dispatch({ type: ToggleType.ON })
    } else {
      dispatch({ type: ToggleType.OFF })
    }
  }, [rawJsonStatus])

  const handleFreePass = async () => {
    try {
      const query = mutateQuery?.(passage)
      await mutate(query, { returnPromise: true })
      refresh()
    } catch (err) {
      notify((err as Error)?.message, 'error')
    }
  }

  return (
    <>
      <ConfirmDialog
        open={confirmDialogOpen}
        confirm={handleFreePass}
        source="freePassForward"
        cancel={() => setConfirmDialogOpen(false)}
        action={type === ToggleType.OFF ? 'set' : 'remove'}
      />
      <Button
        disabled={
          !hasAuthority(Authority.EDIT_PASSAGES) ||
          loading ||
          mutationLoading ||
          disabled === true
        }
        className={classes.button}
        variant="contained"
        onClick={() => setConfirmDialogOpen(true)}
        label={text}
        useSmallVersionBreakpoint={false}
      />
    </>
  )
}
