import { createStyles, makeStyles, Theme } from '@material-ui/core'
import * as React from 'react'
import { useEffect, useReducer } from 'react'
import {
  sanitizeFetchType,
  UPDATE,
  UpdateParams,
  useMutation,
  useNotify,
  useRecordContext,
  useRefresh,
  useShowContext,
} from 'react-admin'
import { Authority } from '../../../core/auth/Authority'
import { BookingUpdateRequest } from '../../../core/bookings/booking-update.request'
import { ToggleType } from '../../../core/bookings/toggle-booking-blockade-button'
import {
  blockTicketButtonInitialState,
  toggleTicketButtonReducer,
} from '../../../core/bookings/toggle-booking-blockade-button.reducer'
import { BookingDto } from '../../../core/dto/booking.dto'
import { BookingDisabledOption } from '../../../core/enum/BookingDisabledOption'
import { ResourceName } from '../../../core/ResourceName'
import Button from '../../common/customized-mui-components/Button'
import { useHasAuthority } from '../../hooks/useHasAuthority'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    button: {
      color: theme.palette.common.white,
      marginLeft: theme.spacing(1),
    },
  }),
)

export const ToggleBookingBlockadeButton = () => {
  const classes = useStyles()
  const notify = useNotify()
  const hasAuthority = useHasAuthority()
  const { loading } = useShowContext()
  const [{ type, text, mutateParam }, dispatch] = useReducer(
    toggleTicketButtonReducer,
    blockTicketButtonInitialState,
  )
  const { id: bookingId, disabled } = useRecordContext<BookingDto>()
  const [mutate, { loading: mutationLoading }] = useMutation()
  const refresh = useRefresh()

  useEffect(() => {
    if (disabled === null) {
      dispatch({ type: ToggleType.BLOCK })
    } else if (disabled === BookingDisabledOption.ADMIN) {
      dispatch({ type: ToggleType.UNBLOCK })
    } else {
      dispatch({ type: ToggleType.DISABLED })
    }
  }, [disabled])

  const onClick = async () => {
    try {
      await mutate(
        {
          type: sanitizeFetchType(UPDATE),
          resource: ResourceName.BOOKINGS,
          payload: {
            data: { id: bookingId, disabled: mutateParam },
          } as UpdateParams<BookingUpdateRequest>,
        },
        { returnPromise: true },
      )
      refresh()
    } catch (err) {
      notify((err as Error)?.message, 'error')
    }
  }

  return (
    <Button
      disabled={
        !hasAuthority(Authority.EDIT_BOOKINGS) ||
        type === ToggleType.DISABLED ||
        loading ||
        mutationLoading
      }
      className={classes.button}
      variant="contained"
      onClick={onClick}
      label={text}
      useSmallVersionBreakpoint={false}
    />
  )
}
