import { sanitizeFetchType, UPDATE } from 'react-admin'
import { AccessUsingDto } from '../dto/access-using.dto'
import { ResourceName } from '../ResourceName'
import {
  ToggleButtonAction,
  ToggleButtonState,
  ToggleType,
} from './access-using-activation-button'

type Mutation = {
  type: string
  resource: string
  // eslint-disable-next-line @typescript-eslint/ban-types
  payload?: object
}

const deactivateMutation = ({ id }: AccessUsingDto): Mutation => ({
  type: sanitizeFetchType(UPDATE),
  resource: ResourceName.ACCESS_USINGS,
  payload: {
    data: {
      id,
      active: false,
    },
  },
})

const activateMutation = ({ id }: AccessUsingDto): Mutation => ({
  type: sanitizeFetchType(UPDATE),
  resource: ResourceName.ACCESS_USINGS,
  payload: {
    data: {
      id,
      active: true,
    },
  },
})

type State = {
  mutateQuery?: (event: AccessUsingDto) => Mutation
} & ToggleButtonState

export const activationButtonInitialState = {
  type: ToggleType.INACTIVE,
  text: 'resources.access-usings.actions.activate',
}

export const toggleActivationButtonReducer = (
  state: State,
  { type }: ToggleButtonAction,
): State => {
  switch (type) {
    case ToggleType.ACTIVE: {
      return {
        type,
        text: 'resources.access-usings.actions.deactivate',
        mutateQuery: deactivateMutation,
      }
    }
    case ToggleType.INACTIVE: {
      return {
        type,
        text: 'resources.access-usings.actions.activate',
        mutateQuery: activateMutation,
      }
    }
    default:
      throw new Error()
  }
}
