import { sanitizeFetchType } from 'react-admin'
import { EngineDto } from '../../dto/device/engine/engine.dto'
import { ResourceName } from '../../ResourceName'
import {
  ToggleButtonAction,
  ToggleButtonState,
  ToggleType,
} from './engine-lock-button'

type Mutation = {
  type: string
  resource: string
  // eslint-disable-next-line @typescript-eslint/ban-types
  payload?: object
}

const lockMutation = ({ id }: EngineDto, lockName?: string): Mutation => ({
  type: sanitizeFetchType('lock'),
  resource: ResourceName.ENGINES,
  payload: {
    engineId: id,
    lockName,
  },
})

const unlockMutation = ({ id }: EngineDto): Mutation => ({
  type: sanitizeFetchType('unlock'),
  resource: ResourceName.ENGINES,
  payload: {
    engineId: id,
  },
})

type State = {
  mutateQuery?: (engine: EngineDto, lockName?: string) => Mutation
} & ToggleButtonState

export const engineLockButtonInitialState = {
  type: ToggleType.UNLOCK,
  text: 'resources.engines.actions.unlock',
}

export const engineLockButtonReducer = (
  state: State,
  { type }: ToggleButtonAction,
): State => {
  switch (type) {
    case ToggleType.LOCK: {
      return {
        type,
        text: 'resources.engines.actions.lock',
        mutateQuery: lockMutation,
      }
    }
    case ToggleType.UNLOCK: {
      return {
        type,
        text: 'resources.engines.actions.unlock',
        mutateQuery: unlockMutation,
      }
    }
    default:
      throw new Error()
  }
}
